.timeline {
  display: block;
  width: 4px;

  position: absolute;
  left: 50%;
  top: 0;
  bottom: rem(180px);
  z-index: 0;

  padding-top: rem(230px);
  margin-left: -2px;

  background: linear-gradient(to bottom, rgba(#bfeafc, 0) 0%, #bfeafc 15%, #bfeafc 85%, rgba(#bfeafc, 0));

  &.time-line-arrow-down {
    background: linear-gradient(to bottom, rgba(#bfeafc, 0) 0%, #bfeafc 15%);

    &:before {
      content: '';
      display: block;
      width: 0;
      height: 0;
      border-top: 22px solid #bfeafc;
      border-bottom: 22px solid transparent;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      position: absolute;
      left: 50%;
      bottom: rem(-60px);
      z-index: 0;
      margin-left: -7px;
    }
  }

  > .tick {

    cursor: default;

    display: inline-block;
    width: rem(60px);
    height: rem(60px);

    position: relative;
    left: 50%;

    margin-bottom: 80px;
    margin-left: rem(-60px / 2);

    @include background('images/card-arrows.png') {
      background-repeat: no-repeat;
      background-size: percentage(300 / 60) percentage(300 / 60);
      background-position: rem(-120px) rem(-120px);
    }

  }

}