.tag-cloud {

  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  margin: 0 -0.25rem $tag-cloud-margin-bottom -0.25rem;

  //margin-left: -0.25em;
  //margin-right: -0.25em;
  //margin-top: 0;
  //margin-bottom: $paragraph-margin-bottom;

  font-size: 87.5%;
  font-weight: 400;
  line-height: 1.5;

  > hr {
    width: 100%;
    height: 0;
    margin: 0;
    border: none;
  }

  > .item {
    display: inline-block;
    margin: 0.25em;
    padding: 0 0.5em;
    color: $blog;
    background: rgba($blog, 0.1);
    border-radius: $border-radius-sm;
  }

  &.text-left {
    justify-content: flex-start;
  }

  &.text-right {
    justify-content: flex-end;
  }

  &.text-center {
    justify-content: center;
  }

  &.with-border {
    .item {
      border: 1px solid $blog;
    }
  }

  &.white-only {
    .item {
      background: transparent;
      border-color: $white;
      color: $white;
    }
  }

}