.section-base-client-logos {

  margin-bottom: 0 !important;

  .main {

    .title {
      margin-bottom: 3.5rem;
    }

    .logos {

      margin: 0 0 2.5rem 0;

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      @include media-breakpoint-up(xs) {justify-content: center;}
      @include media-breakpoint-up(lg) {justify-content: space-between;}

      .item {

        margin: 0 1.5rem 1rem;

        transition: transform 0.15s;
        transform: scale3d(1, 1, 1);

        // Media query disables CSS animations on tablets and phones
        @media (pointer: fine) {
          &:hover {
            transform: scale3d(1.1, 1.1, 1.1);
          }
        }

      }

    }

  }

}