.badge {

  border-width: (1 / 16 + rem);
  border-style: solid;
  font-weight: 400;
  background: $white;
  //white-space: normal;

  @each $color, $value in $theme-colors {
    &.badge-#{$color} {
      border-color: rgba($value, 0.85);
      color: $value;
    }
  }

  &.badge-bold {
    border-width: (2 / 16 + rem);
    font-weight: 500;
  }

}