.audio-player {

  display: block;
  height: rem(2 * 132px);

  position: absolute;
  bottom: rem(100px);
  right: rem(520px);

  > .time-label {
    position: absolute;
    top: 0;
    left: 0;

    display: inline-block;
    width: 100%;

    font-family: $font-family-monospace;
    font-size: $font-size-base * 0.85;
    font-weight: 500;
    color: rgba($white, 0.75);

    white-space: nowrap;
    text-align: center;
    cursor: default;

    opacity: 0;
    transform: translate3d(0, -15px, 0) scale3d(0.5, 0.5, 0.5);
    transition: opacity 0.15s, transform 0.15s;
  }

  > .play-status {
    display: inline-block;
    width: rem(2 * 44px);
    height: rem(2 * 44px);
    line-height: rem(2 * 44px);

    position: absolute;
    top: 0;
    left: 0;

    color: $white;
    text-align: center;

    opacity: 0;
    transform: translate3d(-10px, 20px, 0) scale3d(0, 0, 0);
    transform-origin: center center;
    transition: opacity 0.15s, transform 0.15s;

    &:before {
      @extend .icon:before;
      @extend .icon-player-sound:before;
      font-size: 1.35rem;
      font-weight: normal;
    }
  }

  > .button-stop {
    display: inline-block;
    width: rem(2 * 44px);
    height: rem(2 * 44px);
    line-height: rem(2 * 44px);

    position: absolute;
    top: 0;
    right: 0;

    border: none; // Override button styles
    background: none; // Override button styles
    outline: none; // Override button styles

    color: $white;
    cursor: pointer;

    opacity: 0;
    transform: translate3d(10px, 20px, 0) scale3d(0, 0, 0);
    transform-origin: center center;
    transition: opacity 0.15s, transform 0.15s;

    &:before {
      @extend .icon:before;
      @extend .icon-page-cancel:before;
      font-size: 1.25rem;
      font-weight: bold;
    }
  }

  > .button-play {

    display: inline-block;
    width: rem(250px);
    height: rem(250px);
    padding: 0;
    margin: 0;
    border: none; // Override button styles
    border-radius: 50%;

    // overflow: hidden;

    cursor: pointer;
    outline: none;

    line-height: rem(250px);
    font-size: rem(250px);

    transition: background-color 0.15s, opacity 0.15s, transform 0.15s;
    color: $white;
    background-color: rgba($white, 0.1);
    opacity: 0.75;
    transform: translate3d(0, 0, 0) scale3d(1, 1, 1);

    > .icon,
    > .radial-progress,
    > .radial-border {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }

    > .icon {
      transition: opacity 0.15s, transform 0.15s;
      &.icon-player-play {opacity: 1; transform: scale3d(1, 1, 1);}
      &.icon-player-pause {opacity: 0; transform: scale3d(0, 0, 0);}
    }

    > .radial-progress {

      // Set size of the radial progress
      font-size: rem(25px);

      transition: opacity 0.15s;
      opacity: 0;
    }

    > .radial-border {
      border-radius: 50%;
      border: 5px solid $white;

      transition: opacity 0.15s;
      opacity: 1;
      //transform: scale3d(1, 1, 1);
    }

    &:hover {
      color: rgba($white, 1);
      background-color: rgba($white, 0.25);
      opacity: 1;
      transform: translate3d(0, 0, 0) scale3d(1.065, 1.065, 1.065);

      .radial-border {
        border-color: rgba($white, 1);
      }
    }

  }

  &.playing {

    > .time-label {
      opacity: 1;
      transform: translate3d(0, -35px, 0) scale3d(1, 1, 1);
    }

    > .play-status {
      opacity: 1;
      transform: translate3d(-50px, -10px, 0) scale3d(1, 1, 1);
    }

    > .button-stop {
      opacity: 0.75;
      transform: translate3d(50px, -10px, 0) scale3d(1, 1, 1);
    }

    > .button-play {
      > .icon-player-play {opacity: 0; transform: scale3d(0, 0, 0);}
      > .icon-player-pause {opacity: 1; transform: scale3d(1, 1, 1);}
      > .radial-border {opacity: 0;}
      > .radial-progress {opacity: 1;}
    }

    > .button-stop {
      &:hover {
        transform: translate3d(50px, -10px, 0) scale3d(1.2, 1.2, 1.2);
        opacity: 1;
      }
    }

  }

  &.paused {

    > .time-label {
      opacity: 1;
      transform: translate3d(0, -35px, 0) scale3d(1, 1, 1);
    }

    > .play-status {
      opacity: 0.45;
      transform: translate3d(-50px, -10px, 0) scale3d(1, 1, 1);
    }

    > .button-stop {
      opacity: 0.75;
      transform: translate3d(50px, -10px, 0) scale3d(1, 1, 1);
    }

    > .button-play {
      > .icon-player-play {opacity: 1; transform: scale3d(1, 1, 1);}
      > .icon-player-pause {opacity: 0; transform: scale3d(0, 0, 0);}
      > .radial-border {opacity: 0;}
      > .radial-progress {opacity: 1;}
    }

    > .button-stop {
      &:hover {
        transform: translate3d(50px, -10px, 0) scale3d(1.2, 1.2, 1.2);
        opacity: 1;
      }
    }
  }

}