.face {

  font-size: rem(303px);

  display: inline-block;
  width: 1em;
  height: 1em;

  border-radius: 50%;

  @include background('images/client-faces.jpg', $lazy: true) {
    background-size: (1875 / 303 + em) (640 / 303 + em);
  }

  &.face-1 {background-position: 0 0}
  &.face-2 {background-position: (-393 / 303 + em) 0}
  &.face-3 {background-position: (-786 / 303 + em) 0}
  &.face-4 {background-position: (-1179 / 303 + em) 0}
  &.face-5 {background-position: (-1572 / 303 + em) 0}
  &.face-6 {background-position: (-192 / 303 + em) (-337 / 303 + em)}
  &.face-7 {background-position: (-585 / 303 + em) (-337 / 303 + em)}
  &.face-8 {background-position: (-978 / 303 + em) (-337 / 303 + em)}
  &.face-9 {background-position: (-1371 / 303 + em) (-337 / 303 + em)}

}