.section-base-client-faces {

  .main {

    .title {
      margin-bottom: 4.5rem;
    }

    .faces {

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;

      margin-bottom: 3.5rem;

      .item {

        margin: 0 0 1rem;

        width: rem(360px);
        height: rem(360px);
        text-align: center;
        cursor: pointer;

        a {
          text-decoration: none;
        }

        .face {
          font-size: rem(180px);
          box-shadow: inset 0 0 0 1px $gray-400;
        }

        .name {
          display: block;
          font-weight: 500;
          color: $black;
          white-space: nowrap;
        }

        .role {
          display: block;
          font-weight: 300;
          color: $gray-500;
          white-space: nowrap;
        }

        transition: transform 0.15s;
        transform: scale3d(1, 1, 1);

        // Media query disables CSS animations on tablets and phones
        @media (pointer: fine) {
          &:hover {
            transform: scale3d(1.1, 1.1, 1.1);
          }
        }

      }

    }
  }
}