.star-rating {

  position: relative;
  margin: 1rem 0 1.5rem 0;
  font-size: rem(56px);
  line-height: rem(56px);
  width: 164px;

  &:before, &:after {
    @extend .icon:before;
    content: '\E007\E007\E007\E007\E007';
    letter-spacing: 0.25rem;
    margin-left: 0.25rem;
  }

  &:before {
    color: #ecee3b;
    text-shadow: none;
  }

  &:after {
    color: #3f74ad;
    text-shadow: 0 -1px #444;
    position: absolute;
    top: 0;
    right: 0;
  }

  &-0:after {content: '\E007\E007\E007\E007\E007';}
  &-1:after {content: '\E007\E007\E007\E007';}
  &-2:after {content: '\E007\E007\E007';}
  &-3:after {content: '\E007\E007';}
  &-4:after {content: '\E007';}
  &-5:after {content: '';}

}

