.section-base-subscription {

  margin-bottom: 0 !important;

  .wrap {
    padding: 3rem 1.5rem 3.5rem 1.5rem;
    border-top: 1px solid $border-color;
  }

  .main {
    padding-bottom: 1.5rem;
    text-align: center;

    @include media-breakpoint-up(lg) {
      padding-bottom: 0;
      text-align: left;
    }

    .title {
      color: $primary;
    }

    .words {
      margin: 0;
    }

  }

  .side {
    position: relative;
    max-width: rem(2 * 360px);
    margin: 0 auto;
    .form-control {
      width: 100%;
      padding-right: rem(2 * 115px);
    }
    .btn {
      position: absolute;
      right: 0;
      &:before {
        content: '|';
        position: absolute;
        left: -4px;
        color: $border-color;
      }
    }
  }

  .done {

    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2000;

    padding: 4rem 1.5rem;

    background: $white;

    text-align: center;

    .title {
      color: $primary;
    }
  }

}