/***************************************
              FORM STYLES
***************************************/

.form {

  width: 100%;

  padding-top: 1rem;

  //padding: rem(90px);
  //@include media-breakpoint-up(sm) {
  //  padding: rem(120px);
  //}

  .form-group {
    margin-bottom: 3rem;
  }

  .form-control, .btn {
    &:focus, &:active, &:focus:active,
    &.focus, &.active, &.focus.active {
      box-shadow: $outline-blue;
    }
  }

  //.error {
  //  float: right;
  //  font-size: small;
  //  margin-top: $form-text-margin-top;
  //}

}

.form-gray {

  padding: rem(90px);

  color: $body-color;
  background-color: $light;
  border-radius: $border-radius-lg;

  box-shadow: $box-shadow;

  //.error {color: $danger;}

  .form-control, .btn {
    &:focus, &:active, &:focus:active,
    &.focus, &.active, &.focus.active {
      box-shadow: $outline-blue;
    }
  }

  // Use standard border color for both valid and invalid controls
  //
  .form-control.is-valid,
  .form-control.is-invalid,
  .form-control:valid,
  .form-control:invalid
  {
    border-color: $input-border-color;
  }

  .valid-feedback, .invalid-feedback,
  .valid-tooltip, .invalid-tooltip {
    color: theme-color("danger");
  }

}

.form-blue {

  padding: rem(90px);

  color: $white;
  background-image: $gradient-blue;
  border-radius: $border-radius-lg;

  //.error {color: $white;}

  .form-control, .btn {
    border: 1px solid transparent;
    &:focus, &:active, &:focus:active,
    &.focus, &.active, &.focus.active {
      border: 1px solid $primary;
      box-shadow: $outline-white;
    }
  }

  // Use standard border color for both valid and invalid controls
  //
  .form-control {
    // border-color: $primary !important;
  }

  .valid-feedback, .invalid-feedback,
  .valid-tooltip, .invalid-tooltip {
    color: theme-color("light");
  }

}

/***************************************
              VALIDATION
***************************************/

.form-group {
  position: relative;
}

.valid-feedback,
.invalid-feedback
{
  position: absolute;
  top: 0;
  right: 0;
  width: auto;
  text-align: right;

  .form-row & {
    right: $form-grid-gutter-width / 2;
  }
}