.logo {
  font-size: rem(75.2px);

  display: inline-block;
  height: 1em;

  @include background('images/client-logos.png', $lazy: true) {
    background-size: (2200px / 98px + em) (100px / 98px + em);
  }

  &.logo-1 {width: (216px / 98px + em); background-position: (0px / 98px + em) (-3px / 98px + em);}
  &.logo-2 {width: (473px / 98px + em); background-position: (-365px / 98px + em) (-4px / 98px + em);}
  &.logo-5 {width: (374px / 98px + em); background-position: (-988px / 98px + em) (-14px / 98px + em);}
  &.logo-3 {width: (255px / 98px + em); background-position: (-1518px / 98px + em) (-4px / 98px + em);}
  &.logo-4 {width: (274px / 98px + em); background-position: (-1926px / 98px + em) (-6px / 98px + em);}

}