// Fonts
$font-family-base:        'Montserrat', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;

// Font sizes
$font-size-base:          1rem;
$font-size-lg:            $font-size-base * 1.25;
$font-size-sm:            $font-size-base * 0.875;

// Line heights
$line-height-base:        1.75;
$line-height-lg:          1.5;
$line-height-sm:          1.5;

// Font weights
$font-weight-light:       300;
$font-weight-normal:      400;
$font-weight-bold:        500;

// Black/white
$black:                   #000000;
$white:                   #ffffff;

// Gray colors
$gray-100:                #f8f9fa;
$gray-200:                #e9ecef;
$gray-300:                #dee2e6;
$gray-400:                #ced4da;
$gray-500:                #adb5bd;
$gray-600:                #6c757d;
$gray-700:                #495057;
$gray-800:                #343a40;
$gray-900:                #212529;

// Palette colors
$blue:                    #007bff; // 3.97 : 1 #0072F0
$indigo:                  #6610f2; // 7.18 : 1 #8D54F0
$purple:                  #6f42c1; // 6.51 : 1 #8863C8
$pink:                    #e83e8c; // 3.81 : 1 #E02178
$red:                     #dc3545; // 4.52 : 1 #DC3646
$orange:                  #fd7e14; // 2.57 : 1 #BE5A14
$yellow:                  #ffc107; // 1.63 : 1 #98700A
$green:                   #28a745; // 3.13 : 1 #288832
$teal:                    #20c997; // 2.12 : 1 #0A8764
$cyan:                    #17a2b8; // 3.04 : 1 #118297

// Theme colors
$primary:                 #009ced; // 3.00 : 1 #007DBC - 009ced
$secondary:               #00ac47; // 3.00 : 1 #008943 - 00ac47
$light:                   #f0f8ff;
$dark:                    $gray-600;

// Section colors
$consulting:              #ff6129; // 3.00 : 1 #DB3C00
$management:              #00ac47; // 3.00 : 1 #008943
$development:             #009ced; // 3.00 : 1 #007DBC
$blog:                    #ee50ec; // 3.00 : 1 #D212C8

// Border color
$border-color:            $gray-300;

// Body colors
$body-bg:                 $white;
$body-color:              $gray-600;

// Link colors
$link-color:              $primary;
$link-hover-color:        darken($link-color, 15%);

// Link hovers
$emphasized-link-hover-darken-percentage: 15%;

// Input colors
$input-placeholder-color: $gray-400;
$input-plaintext-color:   $gray-600;

// Table colors
$table-accent-bg:         $light;
$table-hover-color:       $white;
$table-hover-bg:          $primary;

// Button sizes
$input-btn-padding-y-sm:      0.25rem;
$input-btn-padding-x-sm:      0.5rem;
$input-btn-font-size-sm:      $font-size-sm;
$input-btn-line-height-sm:    $line-height-sm;
$input-btn-padding-y-lg:      0.75rem;
$input-btn-padding-x-lg:      1rem;
$input-btn-font-size-lg:      $font-size-lg;
$input-btn-line-height-lg:    $line-height-lg;

// Shadows
$box-shadow-sm:           0 6px 12px -2px rgba(40,40,80,0.25), 0 2px 6px -2px rgba(0,0,0,0.3), 0 -3px 6px -2px rgba(0,0,0,0.1);
$box-shadow:              0 14px 28px -6px rgba(40,40,80,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.1);
$box-shadow-lg:           0 30px 60px -12px rgba(40,40,80,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.1);

// Focus outlines
$outline-blue:            0 0 0 0.1875rem rgba($primary, 0.25);
$outline-white:           0 0 0 0.1875rem rgba($white, 0.35);

// Border radius
$border-radius-sm:        rem(2 * 2px);
$border-radius:           rem(2 * 5px);
$border-radius-lg:        rem(2 * 10px);
$border-radius-xl:        rem(2 * 25px);

// Gradients
$gradient-blue:           linear-gradient(to bottom right, #00c9fe, #008acc);
$gradient-green:          linear-gradient(to bottom right, #94d8ef, #00b39f);
$gradient-mixed:          linear-gradient(to right, #67b762, #61abdf);
$gradient-footer:         linear-gradient(to bottom right, #0076d0, #3054a2);

// Grid system
$grid-columns:            12;
$grid-gutter-width:       60px;

// Spacing
$spacer:                  1rem;
$form-grid-gutter-width:  2rem;

// Cards
$card-border-radius:      $border-radius-lg;
$card-border-color:       $border-color;
$card-spacer-x:           rem(60px);
$card-spacer-y:           rem(90px);

// Badges
$badge-border-radius:     rem(5px);

// Text spacing
$paragraph-margin-bottom: 1.75rem;
$tag-cloud-margin-bottom: 2.00rem;
$list-margin-bottom:      2.00rem;