@mixin debug() {
  body {
    &:after {
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 2000;
      background: rgba(0, 0, 0, 0.5);
      padding: 4px 12px;
      font-size: 2.5rem;
      color: #FFF;
      @include media-breakpoint-only(xs) {content: 'xs';}
      @include media-breakpoint-only(sm) {content: 'sm';}
      @include media-breakpoint-only(md) {content: 'md';}
      @include media-breakpoint-only(lg) {content: 'lg';}
      @include media-breakpoint-only(xl) {content: 'xl';}
    }
  }
}

@mixin retina() {
  @media
  only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (min--moz-device-pixel-ratio: 2),
  only screen and (min-resolution: 192dpi), // IE9-11 don't support dppx
  only screen and (min-resolution: 2dppx) { // Standardized
    @content;
  }
}

@mixin background($src, $lazy: false) {
  @content;
  $index: str-index($src, '.');
  $name: str-slice($src, 1, $index - 1);
  $ext: str-slice($src, $index + 1);
  html.no-js &, html.no-webp & {background-image: url($name + '.' + $ext);}
  html.webp & {background-image: url($name + '.webp');}
  @include retina {
    html.no-js &, html.no-webp & {background-image: url($name + '@2x.' + $ext);}
    html.webp & {background-image: url($name + '@2x.webp');}
  }
  @if $lazy {
    html.js [data-plugin=lazy-load]:not(.load) & {
      background-image: none !important;
    }
  }
}

@mixin link-color($color) {
  color: $color !important;
  @if $emphasized-link-hover-darken-percentage != 0 {
    @include hover-focus {
      color: darken($color, $emphasized-link-hover-darken-percentage) !important;
    }
  }
}