.link {

  @extend .card-link;
  @extend .text-dark;

  > .icon {
    color: $primary;
    text-decoration: none;
    position: relative;
    &:first-child {margin-right: 0.5rem;}
    &:last-child {margin-left: 0.5rem;}
  }

  // Special case for the like icons
  //

  > .icon.icon-blog-likes-alt:before,
  > .icon.icon-blog-likes:before {
    transition: color 0.15s;
  }

  > .icon.icon-blog-likes-alt:after,
  > .icon.icon-blog-likes:after {
    @extend .icon:before;
    position: absolute;
    right: 0;
    opacity: 0;
    transition: opacity 0.15s;
    z-index: 1;

    // Fix cutoff bug in Chrome during transition
    //
    width: (1em + 0.0625em / 2);
  }

  > .icon.icon-blog-likes-alt {
    color: lighten(#e8335c, 8);
    &:after {@extend .icon-blog-likes:before; color: $primary !important;}
  }

  > .icon.icon-blog-likes {
    color: $primary;
    &:after {@extend .icon-blog-likes-alt:before; color: lighten(#e8335c, 8) !important;}
  }

  &:hover {
    > .icon.icon-blog-likes-alt:before, .icon.icon-blog-likes:before {color: transparent;}
    > .icon.icon-blog-likes-alt:before, .icon.icon-blog-likes:after {opacity: 1;}
  }

}
