.modal-body-projects {

  .info {

    margin-bottom: 2rem;

    .logo {font-size: rem(110px);}
    .tag-cloud {height: 100%;}

    @include media-breakpoint-up(xs) {
      text-align: center;
    }

    @include media-breakpoint-up(lg) {
      .project-logo {text-align: left;}
      .project-tech {text-align: right;}
    }

  }

  .main {

    @include media-breakpoint-up(xs) {
      text-align: center;
    }

    @include media-breakpoint-up(md) {
      text-align: left;
    }

    .summary {

      float: right;
      position: relative;
      background-color: #f4f8ff;
      border-radius: $border-radius-lg;
      padding: 1rem 2rem;
      margin-top: -0.5rem;

      dl {
        margin: 0;
        line-height: rem(72px);
      }

      dt {
        display: inline-block;
        width: 45%;
        z-index: 10;
        font-weight: 500;
        color: $black;
      }

      dd {
        display: inline-block;
        width: 50%;
        margin: 0;
        white-space: nowrap;
        color: $black;
      }

      @include media-breakpoint-up(xs) {
        width: 100%;
        margin-bottom: 1.75rem;
        text-align: center;
        dt {display: none;}
      }

      @include media-breakpoint-up(sm) {
        dt {display: inline-block;}
        text-align: left;
      }

      @include media-breakpoint-up(md) {
        width: 55%;
        margin-left: 2.5rem;
        margin-bottom: 0.75rem;
      }

      @include media-breakpoint-up(lg) {
        width: 45%;
        margin-left: 2.5rem;
      }

      @include media-breakpoint-up(xl) {
        width: 40%;
        margin-left: 2.5rem;
      }

    }

    .line {

      margin-top: rem(60px);
      margin-bottom: 2.5rem;

      display: flex;
      flex-wrap: nowrap;
      flex-direction: row;
      align-items: center;

      white-space: nowrap;
      font-weight: 500;
      color: $black;

      &:before,
      &:after {
        display: inline-block;
        content: '';
        flex: 1 0 auto;
        height: 1px;
        background-color: $border-color;
      }

      &:before {margin-right: 1rem;}
      &:after {margin-left: 1rem;}

      @include media-breakpoint-up(md) {
        &:before {flex: 0 0 50px;}
        &:after {flex: 0 1 100%;}
      }

    }

    .related-reviews {

      margin: 0 rem(60px) 1rem;
      //margin: 0;

      .item {


        min-height: rem(180px);
        margin: 0;
        padding: 0;

        &:not(:last-child) {
          margin-bottom: 1.75rem;
        }

        > .side {flex: 0 0 rem(260px);}
        > .main {flex: 0 1 auto;}

        @include media-breakpoint-up(xs) {
          //flex-direction: column;
          .quote {width: 100%; margin: 0 0 0.5rem 0;}
          //.main {margin: 0;}
          .photo {margin-bottom: 1rem;}
          .face {font-size: rem(200px); box-shadow: inset 0 0 0 1px $gray-400;}
          .name {display: block; color: $black;}
          .name:before {opacity: 0.35;}
        }

        @include media-breakpoint-up(md) {
          display: flex;
          flex-direction: row;
          align-items: center;
          .photo {margin: 0 rem(60px) 0 0;}
          .name {float: right;}
        }
      }
    }
  }
}