.section-blog-post-intro {

  margin-bottom: rem(60px);

  .side {

    .card {

      text-align: left;

      padding: rem(60px);

      .title {
        margin: 0.5rem 0 1rem 0;
        color: $white;
        font-weight: 300;
        line-height: 2.5rem;
      }

      @include media-breakpoint-up(xs) {
        margin-top: rem(-150px);
        padding-left: rem(60px);
        padding-right: rem(60px);
      }

      @include media-breakpoint-up(lg) {
        padding-left: rem(120px);
        padding-right: rem(120px);
      }

    }

    .title {
    }

    .post-info {

      display: inline-flex;

      .item {

        white-space: nowrap;
        color: #99dcf4;

        i {color: #99dcf4; vertical-align: middle; margin-right: 0.125rem;}
        a {color: $white; font-weight: 300; vertical-align: middle;}
        b {color: $white; font-weight: 300; vertical-align: middle;}

        &:not(:last-child):after {
          display: inline-block;
          content: '|';
          padding: 0 0.75rem;
        }

      }

    }



    @include media-breakpoint-up(xs) {
      text-align: center;
      .post-info {flex-direction: column;}
      .post-info .item:after {display: none !important;}
    }

    @include media-breakpoint-up(sm) {
      .post-info {flex-direction: row; flex-wrap: wrap; justify-content: center;}
      .post-info .item:not(:nth-child(2)):after {display: inline-block !important;}
    }

    @include media-breakpoint-up(md) {
      .post-info {width: 85%;}
    }

    @include media-breakpoint-up(lg) {
      text-align: left;
      .star-rating {float: right; margin-left: 3rem;}
      .post-info {width: auto;}
      .post-info .item:after {display: inline-block !important;}
    }

  }

}

.section-blog-post-content {

  position: relative;
  margin-bottom: rem(120px);

  .rellax-container:first-child {
    .shape-1 {top: 0; right: 100%; margin-right: -0.15em; margin-top: -0.65em;}
  }

  .rellax-container:last-child {
    .shape-2 {top: 0; left: 100%; margin-left: 0.30em; margin-top: -1.15em;}
  }

  .row-indent {

    // xs - 500 (max width)
    // sm - 480
    // md - 660
    // lg - 610
    // xl - 705


    @include media-breakpoint-up(xs) {
      padding-left: 0;
      padding-right: 0;
    }

    @include media-breakpoint-up(md) {
      //padding-left: rem(120px);
      //padding-right: rem(120px);
    }

    @include media-breakpoint-up(lg) {
      padding-left: rem(180px);
      padding-right: rem(400px);
    }

    @include media-breakpoint-up(xl) {
      padding-left: rem(270px);
      padding-right: rem(480px);
    }

  }

  .main {
    //padding: 0 rem(480px) 0 rem(270px);
  }

  .image {
    border-radius: $border-radius;
  }

  .share-box {

    display: flex;

    .bold {
      text-transform: uppercase;
      text-align: center;
      font-size: $font-size-sm;
      font-weight: 500;
      color: #8ac8f5;
    }

    .item {

      color: #80b5f2;
      text-align: center;

      width: rem(120px);
      height: rem(120px);
      border: 1px solid #80b5f2;
      //margin-bottom: 1rem;

      background: $white;
      border-radius: 50%;

      font-size: rem(48px);

      .icon {margin: rem(34px);}

      transition: transform 0.15s, color 0.15s;
      transform: none;

      &:hover {
        // transform: scale3d(1.1, 1.1, 1.1);
        color: darken(#80b5f2, 15%);
      }

    }

  }

  @include media-breakpoint-up(xs) {
    .share-box {
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      margin-bottom: 3rem;
      margin-left: 0;
      .bold {width: 100%; margin-bottom: 1rem;}
      .item {margin: 0 0.5rem 1rem 0.5rem;}
      .icon-social-facebook:before {font-size: 78%;}
      .icon-social-medium:before {font-size: 95%;}
      .icon-social-twitter:before {font-size: 92%;}
    }
  }

  @include media-breakpoint-up(lg) {
    .share-box {
      flex-direction: column;
      margin-bottom: 0;
      position: absolute;
      right: rem(-240px);
      .bold {width: auto; margin-bottom: rem(30px);}
      .item {display: block;}
      .icon-social-facebook:before {font-size: 85%;}
    }
  }

}

.section-blog-post-author {

  margin-bottom: rem(180px);

  .photo {
    display: inline-block;
    width: rem(240px + 2 * 8px);
    height: rem(240px + 2 * 8px);
    border-radius: 50%;
    box-shadow: 0 0 0 rem(8px) rgba($primary, 0.35);
  }

  .title {
    display: inline-block;
  }

  .quote {
    display: inline-block;
  }

  .follow-box {

    display: inline-block;

    height: rem(220px);
    padding: rem(40px) rem(60px);

    position: absolute;
    top: rem(-60px);
    right: 0;
    z-index: 100;

    background-image: $gradient-blue;
    color: $white;
    text-align: center;

    .bold {
      margin-bottom: 0.25rem;
    }

    .item {
      color: inherit;
      font-size: 150%;
      margin: 0 0.5rem;
    }

    .icon:before {vertical-align: middle; line-height: 100%;}
    .icon-social-medium:before {font-size: 100%;}
    .icon-social-facebook:before {font-size: 85%;}
    .icon-social-twitter:before {font-size: 92%;}
    .icon-social-rss:before {font-size: 75%;}

  }

  @include media-breakpoint-up(xs) {
    .follow-box {left: 4rem; }
    .quotation {padding-top: 8rem; text-align: center;}
    .group {}
    .photo {display: block; margin: 0 auto 1.5rem auto;}
    .badge {width: auto; margin: 0 auto 0.5rem auto; }
    .title {display: block; margin: 0 auto 1.5rem auto;}
    .quote {width: 100%; text-align: center;}
  }

  @include media-breakpoint-up(sm) {
    .follow-box {left: auto; width: rem(525px);}
    .quotation {padding-top: 6rem; text-align: left;}
    .photo {float: left; margin-right: 2.5rem;}
    .badge {margin-top: 2.5rem;}
    .quote {clear: both; width: 100%; text-align: left;}
  }

  @include media-breakpoint-up(md) {
    .quotation {padding-top: 3.5rem;}
    .badge {margin-bottom: 1rem;}
  }

  @include media-breakpoint-up(lg) {
    .quotation {padding-top: 2.5rem;}
    .photo {width: rem(300px + 2 * 12px); height: rem(300px + 2 * 12px);}
    .badge {margin-top: 0.5rem;}
    .title {margin: 0 auto 1rem auto;}
    .quote {display: block; clear: none; width: auto;}
  }

}

.section-blog-post-comment-list {

  margin-bottom: rem(120px);

  .rellax-container {
    .shape {top: 0; right: 0; margin-right: -0.5em; margin-top: 0.25em;}
  }

  .comment-list {
    margin-top: 5rem;
    @include media-breakpoint-up(md) {margin-left: rem(320px);}
  }

  .comment-list > .comment {

    width: 100%;
    margin-bottom: rem(120px);
    text-align: left;

    @include media-breakpoint-up(xs) {
      > .card-body {text-align: center; padding: rem(60px);}
      > .card-body > .card-title:after {display: none;}
      > .card-body > .card-title > .photo {display: inline-block; font-size: rem(180px); box-shadow: inset 0 0 0 1px $gray-400;}
      > .card-body > .card-title > .title {display: block;}
      > .card-body > .card-title > .badge {display: inline-block; }
      > .card-body > .words {}
      > .card-footer {display: flex; justify-content: space-between;}
    }

    @include media-breakpoint-only(sm) {
      > .card-body {text-align: left;}
      > .card-body > .card-title > .photo {float: left; margin-right: 1.5rem;}
      > .card-body > .card-title > .title {display: inline-block; margin-top: 0.5rem;}
      > .card-body > .card-title > .badge {display: inline-block; margin-top: 0.5rem; margin-bottom: 1rem;}
    }

    @include media-breakpoint-up(md) {
      > .card-body {text-align: left;}
      > .card-body > .card-title:after {display: block;}
      > .card-body > .card-title > .photo {position: absolute; left: -($card-spacer-x + rem(320px - 90px)); top: 50%; transform: translate(-50%, -50%);}
      > .card-body > .card-title > .title {}
      > .card-body > .card-title > .badge {position: absolute; right: 0; top: 50%; transform: translate(0, -50%);}
    }

  }

