html.no-js {
  .rellax {
    opacity: 1 !important;
    transition: none !important;
  }
}

.rellax-container {
  position: relative;
  width: 100%;
}

.static {
  position: relative;
}

.rellax {

  position: absolute;

  &.shape {

    // Shapes are hidden initially
    opacity: 0;

    .rellax-animate & {
      transition: transform 1000ms ease, opacity 500ms ease;
      transition-delay: 100ms;
      @media (pointer: fine) {transition-delay: 50ms;}
    }

    .rellax-visible & {
      opacity: 1;
    }

  }

  &.image, &.block {

    .rellax-animate & {
      transition: transform 1000ms ease;
      transition-delay: 100ms;
      @media (pointer: fine) {transition-delay: 50ms;}
    }
  }

}

