.section-home-start {

  height: rem(1520px);

  .welcome {
    position: absolute;
    right: rem(-284px);
    width: rem(1810px);
    height: rem(1260px);

    //@include background('images/home-photo.jpg') {
    //  background-size: auto 100%;
    //  background-repeat: no-repeat;
    //  background-position: right top;
    //}

    // Using <img> tag for consistent WebP support.
    // Specifically, in NoJS mode it'll use WebP.
    // All background images use JPG files in NoJS.

    .image {
      position: absolute;
      right: 0;
      z-index: -1;
      width: rem(1810px);
      height: rem(1260px);
      user-select: none;
    }
  }

  .intro-1 {

    &:before {
      content: '';
      width: 50%;
      height: 100%;
      background: rgba(0, 0, 0, 0.25);
      position: absolute;
      left: 100%;
      top: 0;
      z-index: 0;
    }

    @include media-breakpoint-up(xs) {

      text-align: center;

      background: rgba(0, 0, 0, 0.25);
      border-top-left-radius: $border-radius-lg;
      border-bottom-left-radius: $border-radius-lg;

      display: block;
      width: rem(2 * 360px);
      margin-left: rem(-360px);

      position: absolute;
      left: 50%;
      right: auto;
      bottom: 24rem;

    }

    @include media-breakpoint-up(sm) {
      width: rem(2 * 360px);
      margin-left: rem(-360px + 80px);
      left: 50%;
      right: auto;
      text-align: right;
    }

    @include media-breakpoint-up(md) {
      width: rem(2 * 440px);
      left: auto;
      bottom: 22rem;
      right: rem(60px);
      text-align: right;
      &:before {display: none;}
      background: transparent;
    }

    @include media-breakpoint-up(lg) {
      right: auto;
      bottom: 16rem;
      left: 0;
      margin-left: rem(60px);
      text-align: left;
    }

    .title {
      margin: 1.5rem 0 0 0;
      color: $white;
      font-size: 2rem;
      font-weight: 300;
      line-height: 1.125;
      white-space: nowrap;
      @include media-breakpoint-up(sm) {font-size: 2.25rem;}
      @include media-breakpoint-up(md) {font-size: 2.75rem;}
      @include media-breakpoint-up(lg) {color: $black;}
      @include media-breakpoint-up(xl) {font-size: 3.5rem;}
    }

    .promo {
      margin: 0.75rem 0.25rem 1.5rem 0.25rem;
      font-size: 1rem;
      font-weight: 300;
      color: $white;
      white-space: nowrap;
      @include media-breakpoint-up(md) {font-size: 1.25rem;}
      @include media-breakpoint-up(lg) {margin: 1.5rem 0 0.75rem 0; color: $body-color;}
    }

  }

  .intro-2 {

    display: none;
    position: absolute;
    left: 30px;
    bottom: 0;
    margin-left: 0;

    @include media-breakpoint-up(sm) {
      display: block;
    }

    @include media-breakpoint-up(md) {
      bottom: 12rem;
      margin-left: 2rem;
    }

    @include media-breakpoint-up(lg) {
      bottom: 8rem;
      margin-left: 0;
    }

    .button-estimate {
      display: none;
      margin-right: 1.5rem;

      @include media-breakpoint-up(lg) {
        display: inline-block;
      }

    }

    .button-linkedin {

      padding: 0;

      font-size: $font-size-lg;
      color: $primary;
      text-decoration: none;

      @include transition($btn-transition);

      @include media-breakpoint-up(lg) {
        padding-left: 1rem;
        padding-right: 1rem;
      }

      &:hover {
        color: darken($primary, 7.5%);
      }

      .icon {

        margin-top: -0.25rem;
        margin-right: 0.75rem;
        font-size: rem(64px);
        position: relative;

        &:after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          background-image: linear-gradient(to top, rgba($white, 0), rgba($white, 0.5));

          // We use left: -1px instead of traditional left: 0
          // This is mainly to fix rendering in Safari, while FireFox and Chrome are good too.
          left: -1px;
        }
      }

    }

    .resume-download {
      display: inline-block;
      margin-top: 1rem;

      @include media-breakpoint-up(xs) {margin-left: 0.125rem;}
      @include media-breakpoint-up(lg) {margin-left: 1rem;}

      > a {font-weight: 500; @include link-color($secondary);}
      > b {padding: 0 0.5rem; color: #dedede; font-weight: 300;}
    }

  }
}

.section-home-intro,
.section-home-consulting,
.section-home-development,
.section-home-management,
.section-home-blog,
.section-home-carousel,
.section-base-client-logos {
  @include media-breakpoint-up(xs) {margin: 0 0 rem(150px) 0;}
  @include media-breakpoint-up(md) {margin: 0 0 rem(200px) 0;}
  @include media-breakpoint-up(lg) {margin: 0 0 rem(300px) 0;}
}

.section-home-intro {

  .side {

    @include media-breakpoint-up(xs) {
      display: none;
    }

    @include media-breakpoint-up(sm) {
      display: block;
      margin-top: rem(90px);
    }

    @include media-breakpoint-up(md) {
      margin-top: 0;
    }

    .card {

      //@extend .card-intro;
      //@extend .card-intro-shape;

      width: calc(50% - 2.5rem / 2);

      text-align: left;
      cursor: pointer;
      background: $white;

      color: $body-color;
      text-decoration: none;

      // Fix IE 10 hover
      z-index: 1;

      &:before {
        font-size: rem(350px);
      }

      > .icon {display: block;}
      > .title {display: block; margin-bottom: 0.75rem; font-weight: 500;}
      > .words {font-size: 87.5%; line-height: 1.5rem; margin-bottom: 0;}

      // Media query resolves double-tap problem on tablets and phones.
      // Instead of showing hover state on first tap, it immediately navigates.
      //
      @media all and
      (-ms-high-contrast: none),      // IE 10+
      (-ms-high-contrast: active),
      (pointer: fine) {               // No touchscreen

        position: relative;
        overflow: hidden;

        transition: transform 0.15s ease;

        &:hover {

          // Fix background rendering width in Chrome
          background-size: 105% auto;
          background-image: $gradient-blue;

          transform: translateY(-3px);
          box-shadow: $box-shadow-lg;

          > .icon,
          > .title,
          > .words {
            color: $white;
          }

          > .icon-box-animated {

            > i {
              animation-play-state: running !important;
            }

            &:after {

              // In order to make our 3d animation realistic, we need to
              // make icon backing conform box gradient, but cannot use
              // opacity. Otherwise particles behind icon backing will be
              // seen, which is no desired.

              background-size: 105% auto;
              background-image: linear-gradient(to bottom right, lighten(#22c9fa, 6), darken(#22c9fa, 4));
            }
          }
        }
      }

      // Forcefully disable beautiful 3d animation in IE 10-11,
      // since preserve-3d isn't supported.
      // More: https://caniuse.com/#search=3d%20transforms
      //
      @media all and
      (-ms-high-contrast: none),      // IE 10+
      (-ms-high-contrast: active) {
        &:hover {
          > .icon-box-animated > i {
            animation-play-state: paused !important;
          }
        }
      }

      // We could probably show 3d particle animations in EDGE 18+,
      // but let's stick with turning off for all EDGE versions for now.
      //
      @supports (-ms-ime-align:auto) { // EDGE 12+
        &:hover {
          > .icon-box-animated > i {
            animation-play-state: paused !important;
          }
        }
      }

      @include media-breakpoint-up(sm) {
        &.card-consulting {float: left; margin-top: 5.5rem;}
        &.card-management {float: left; clear: left; margin-bottom: 0;}
        &.card-development {float: right;}
        &.card-blog {float: right; clear: right; margin-bottom: 0; margin-top: -5.5rem;}
      }

      @include media-breakpoint-up(md) {
        &.card-consulting {margin-top: 0;}
        &.card-management {}
        &.card-development {margin-top: 5.5rem;}
        &.card-blog {margin-top: 0;}
      }

      @include media-breakpoint-up(lg) {
        &.card-consulting {}
        &.card-management {}
        &.card-development {margin-top: 9.5rem;}
        &.card-blog {}
      }

    }
  }

  .main {

    margin-top: 0;

    @include media-breakpoint-up(sm) {
      margin-top: rem(150px);
    }

    @include media-breakpoint-up(lg) {
      margin-top: rem(400px);
      padding-left: rem(90px);
    }

    @include media-breakpoint-up(xl) {
      padding-left: rem(120px);
    }

    .badge {
      @include media-breakpoint-up(xs) {text-align: left; font-size: 0.9375rem; line-height: 0.9375rem;}
      @include media-breakpoint-up(sm) {text-align: center; font-size: 1.125rem; line-height: 1.125rem;}
    }

  }
}

.section-home-consulting {

  align-items: flex-start;

  .main {

    @include media-breakpoint-up(xs) {
      margin-top: 0;
    }

    @include media-breakpoint-up(md) {
      padding-right: rem(120px);
    }

    @include media-breakpoint-up(lg) {
      margin-top: rem(160px);
    }

    @include media-breakpoint-up(xl) {
      margin-top: rem(250px);
    }

    .rellax-container {

      // home-consulting-sm.jpg - 960x420

      padding-top: percentage(420 / 960);
      margin: rem(96px) 0 rem(56px) 0;

      .image {
        position: absolute;
        top: 0;
        left: 0;
        width: percentage(960 / 960);
        height: percentage(420 / 420);
        border-radius: $border-radius;
        overflow: hidden;
      }

      .shape {
        top: 0;
        left: 100%;
        margin-top: -0.350em;
        margin-left: -0.375em;
      }

    }

  }

  .side {

    .rellax-container {

      // home-consulting.jpg - 1140x1200

      padding-top: percentage(1200 / 1140);

      .image {
        position: absolute;
        top: 0;
        left: 0;
        width: percentage(1140 / 1140);
        height: percentage(1200 / 1200);
        border-radius: $border-radius;
        overflow: hidden;
      }

      .shape {
        top: 0;
        left: 100%;
        margin-top: -0.150em;
        margin-left: -0.375em;
      }
    }
  }
}

.section-home-development {

  .main {

    @include media-breakpoint-up(xs) {
      margin-top: 0;
    }

    @include media-breakpoint-up(md) {
      padding-left: rem(180px);
    }

    @include media-breakpoint-up(lg) {
      margin-top: rem(50px);
      padding-left: rem(260px);
    }

    @include media-breakpoint-up(xl) {
      margin-top: rem(100px);
      padding-left: rem(320px);
    }

    .rellax-container {

      // home-development-sm-1.svg - 880x440
      // home-development-sm-2.svg - 640x300

      padding-top: percentage(440 / 960);
      margin: rem(48px) 0 rem(140px) 0;

      .image-tab-code {

        width: percentage(880 / 960);
        height: percentage(440 / 440);

        position: absolute;
        left: 0;
        top: 0;

        &:before {
          content: '';
          position: absolute;
          top: percentage(27.5 / 190);
          right: 0;
          bottom: 0;
          left: 0;
          z-index: -1;
          border-radius: $border-radius-lg;
          box-shadow: $box-shadow;
        }

      }

      .image-terminal {

        width: percentage(640 / 960);
        height: percentage(300 / 440);

        position: absolute;
        top: 45%;
        right: 0;
        z-index: 100;

        border-radius: $border-radius-lg;
        box-shadow: $box-shadow;
      }

      .shape {
        top: 0;
        right: 100%;
        margin-right: -0.375em;
        margin-top: 0.10em;
      }

    }

    .tag-cloud {
      justify-content: left;
    }

  }

  .side {

    .rellax-container {

      // home-development-1.svg - 1100x960
      // home-development-2.svg - 840x680

      padding-top: percentage(1100 / 1200);

      .image-tab-code {
        position: absolute;
        left: 0;
        top: 0;
        width: percentage(1100 / 1200);
        height: percentage(960 / 1100);
        border-radius: $border-radius-lg;

        &:before {
          content: '';
          position: absolute;
          top: percentage(36 / 442.5);
          right: 0;
          bottom: 0;
          left: 0;
          z-index: -1;
          border-radius: $border-radius-lg;
          box-shadow: $box-shadow;
        }
      }

      .image-terminal {
        position: absolute;
        width: percentage(840 / 1200);
        height: percentage(680 / 1100);
        right: -10%;
        bottom: 0;
        box-shadow: $box-shadow;
        border-radius: $border-radius-lg;
      }

      .shape {
        top: 0;
        right: 100%;
        margin-right: -0.375em;
        margin-top: -0.15em;
      }
    }
  }
}

.section-home-management {

  .main {

    @include media-breakpoint-up(xl) {
      margin-top: 100px;
    }

    .rellax-container {

      // home-management-sm-1.svg - 820x400
      // home-management-sm-2.svg - 680x300

      padding-top: percentage(400 / 960);
      margin: rem(96px) 0 rem(56px) 0;

      .image-gantt-diagram {
        width: percentage(820 / 960);
        height: percentage(400 / 400);
        left: 0;
        top: 0;
        z-index: 102;
      }

      .image-specification {
        width: percentage(680 / 960);
        height: percentage(300 / 400);
        right: 0;
        top: 15%;
        z-index: 101;
      }

      .image {
        position: absolute;
        border-radius: $border-radius-lg;
        box-shadow: $box-shadow;
      }

      .shape {
        top: 0;
        left: 100%;
        margin-top: -0.35em;
        margin-left: -0.375em;
      }

    }

  }

  .side {

    .rellax-container {

      // home-management-1.svg - 840x680
      // home-management-2.svg - 840x680

      padding-top: percentage(900 / 1050);

      .image-gantt-diagram {left: 0; bottom: 0;}
      .image-specification {right: 0; top: 0;}

      .image {
        position: absolute;
        width: percentage(840 / 1050);
        height: percentage(680 / 900);
        border-radius: $border-radius-lg;
        box-shadow: $box-shadow;
      }

      .shape {
        top: 0;
        left: 100%;
        margin-top: -0.150em;
        margin-left: -0.375em;
      }

    }

  }

}

.section-home-blog {

  .main {

    .rellax-container {

      // home-blog-sm.jpg - 960x420

      margin-bottom: rem(56px);

      .image {

        position: absolute;
        z-index: -1;
        top: rem(-100px);
        right: rem(-280px);

        @include media-breakpoint-up(xs) {
          width: rem((576 / 768) * 960px);
          height: rem((576 / 768) * 420px);
        }

        @include media-breakpoint-up(sm) {
          width: rem((768 / 768) * 960px);
          height: rem((768 / 768) * 420px);
        }
      }

      .block {
        margin-top: 3rem;
        padding: 4rem 2.5rem 1rem 2.5rem;
        border-radius: $border-radius;
      }

    }

  }

  .side {

    .rellax-container {

      // home-blog.jpg - 1540x960

      @include media-breakpoint-up(md) {padding-top: percentage(525 / 300);}
      @include media-breakpoint-up(lg) {padding-top: percentage(640 / 510);}

      .image {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 25%;
        border-radius: $border-radius;

        .img-fluid {
          max-width: unset !important;
          height: 100% !important;
        }
      }

      .block {
        position: absolute;
        right: percentage(30 / 510);
        bottom: 0;
      }

    }

  }

  .post-box {

    position: relative;

    //font-size: $font-size-sm;
    //line-height: $line-height-sm;
    color: $white;

    box-shadow: $box-shadow;

    background-image: $gradient-blue;

    @include media-breakpoint-up(md) {
      width: 100%;
      padding: 3.5rem 2.5rem 1rem 2.5rem;
    }

    @include media-breakpoint-up(lg) {
      width: percentage(675 / 510);
      padding: 3.5rem 3.5rem 1rem 10rem;
    }

    @include media-breakpoint-up(xl) {
      width: percentage(675 / 510);
      padding: 3.5rem 5rem 1rem 12.5rem;
    }

    .seal {
      display: block;
      width: rem(152px);
      height: rem(152px);
      position: absolute;
      top: rem(-152px / 2);
      right: rem(90px);
      background: #ff9b48;
      border-radius: 50%;
      font-size: 2rem;
      text-align: center;
      line-height: rem(152px - 2 * 4px);
      border: rem(4px) solid rgba($white, 0.35);
      box-shadow: $box-shadow-sm;
      color: $white;
    }

    p, p > b {
      color: $white;
    }

  }

}

.section-home-carousel {

  position: relative;

  padding-top: rem(140px);
  padding-bottom: rem(140px);

  @include media-breakpoint-up(lg) {
    //width: 70%;
    .container {width: 65%;}
  }

  .review {

    // Prevent margin collapsing
    display: inline-block;
    width: 100%;
    //height: rem(2 * 455px);

    position: relative;

    // margin-top: rem(300px * 0.45);

    @include media-breakpoint-up(xs) {padding: 6.5rem 1.5rem 1.5rem 1.5rem;}
    @include media-breakpoint-up(sm) {padding: 6.5rem 2.5rem 2.5rem 2.5rem;}
    @include media-breakpoint-up(md) {padding: 6.5rem 3.5rem 3.5rem 3.5rem;}

    text-align: center;

    .back {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 2;
      background-color: $white;
      border-radius: $border-radius-lg;
      box-shadow: $box-shadow-lg;

      &:before {
        content: '';
        height: rem(150px);
        width: rem(300px);
        position: absolute;
        top: 0;
        left: 50%;
        margin-left: rem(-300px / 2 + 1px); // This pixel fixes animation in Chrome
        margin-top: rem(-300px * 0.45);
        border-top-left-radius: rem(300px);
        border-top-right-radius: rem(300px);
        background: $white;
        box-shadow: $box-shadow-lg;
        clip: rect(rem(-100px), rem(450px), rem(136px), rem(-450px));
      }
    }

    .next {
      content: '';
      display: inline-block;
      width: 100%;
      height: 35px * (1609 / 1279) + 20px;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;
      background-color: $white;
      border-radius: $border-radius-lg;
      box-shadow: $box-shadow-lg;
      transform: translate3d(0, 35px, 0) scale3d(percentage(1279 / 1609) / 100%, percentage(1279 / 1609) / 100%, percentage(1279 / 1609) / 100%);
      transform-origin: center bottom;
    }

    .photo {
      z-index: 100;
      font-size: 120px;
      position: absolute;
      top: 0;
      left: 50%;
      margin: rem(-108px) 0 0 rem(-120px + 1px); // This pixel fixes animation in Chrome
    }

    .title {
      position: relative;
      z-index: 100;
      margin-bottom: rem(40px);
      color: $black;
    }

    .quote {
      position: relative;
      z-index: 100;
      margin-bottom: rem(60px);
      color: $black;
      font-weight: 300;
      font-style: normal;
      @include media-breakpoint-up(xs) {
        font-size: rem(40px);
        line-height: rem(60px);
      }
      @include media-breakpoint-up(sm) {
        font-size: rem(45px);
        line-height: rem(70px);
      }
      @include media-breakpoint-up(lg) {
        font-size: rem(60px);
        line-height: rem(100px);
      }
    }
  }

  .btn {
    position: relative;
    z-index: 100;
    margin: 0 0.5rem 0.5rem 0.5rem;
  }

  .rellax-container {

    //perspective: 80px;
    //perspective-origin: center center;
    //transform-style: preserve-3d;

    //will-change: transform, opacity;

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    //width: 100%;
    //height: 100%;

    @include media-breakpoint-down(md) {
      display: none;
    }
  }

  .rellax-container > .face {

    box-shadow: 0 0 0 1px $white, $box-shadow;

    @include media-breakpoint-up(lg) {

      margin-bottom: rem(200px);
      //position: absolute;

      transform-origin: center center;

      //bottom: 0;
      //left: 50%;
      //margin-left: -60px;
      //font-size: 120px;

      //style="transform: translate3d(-40vw, -50px, 0) scale3d(0.95, 0.95, 0.95);"
      //style="transform: translate3d(40vw, -130px, 0) scale3d(0.95, 0.95, 0.95);"
      //style="transform: translate3d(-30vw, -570px, 0) scale3d(0.375, 0.375, 0.375);"
      //style="transform: translate3d(35vw, 20px, 0) scale3d(0.45, 0.45, 0.45);"
      //style="transform: translate3d(15vw, -620px, 0) scale3d(0.375, 0.375, 0.375);"
      //style="transform: translate3d(35vw, -340px, 0) scale3d(0.7, 0.7, 0.7);"
      //style="transform: translate3d(-35vw, -290px, 0) scale3d(0.625, 0.625, 0.625);"
      //style="transform: translate3d(40vw, -480px, 0) scale3d(0.55, 0.55, 0.55);"
      //style="transform: translate3d(-40vw, -440px, 0) scale3d(0.45, 0.45, 0.45);"

      &.face-1 {font-size: (226px / 2); margin-left: -0.5em; left: calc((220 / 335) * (100% - 100vw) / 2 + 8px); bottom: (-145px / 2);}     // Andrew
      &.face-2 {font-size: (219px / 2); margin-right: -0.5em; right: calc((220 / 335) * (100% - 100vw) / 2 + 8px); bottom: (25px / 2);}     // Nicolas
      &.face-3 {font-size: (84px / 2); margin-left: -0.5em; left: calc((80 / 335) * (100% - 100vw) / 2 + 8px); bottom: (970px / 2);}        // Ted
      &.face-4 {font-size: (103px / 2); margin-right: -0.5em; right: calc((100 / 335) * (100% - 100vw) / 2 + 8px); bottom: (-210px / 2);}   // Christine
      &.face-5 {font-size: (84px / 2); margin-right: -0.5em; right: calc((-120 / 335) * (100% - 100vw) / 2 + 8px); bottom: (1070px / 2);}   // Manu
      &.face-6 {font-size: (164px / 2); margin-right: -0.5em; right: calc((120 / 335) * (100% - 100vw) / 2 + 8px); bottom: (480px / 2);}    // Aaron
