$page-padding: 'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="434px" height="160px"><path fill-rule="evenodd" fill="rgb(255, 255, 255)" d="M-0.010,0.000 C199.966,0.000 198.775,160.000 433.990,160.000 C107.564,160.000 -0.010,160.000 -0.010,160.000 L-0.010,0.000 Z"/></svg>';

.wrapper {
  overflow: hidden;
}

.container {
  position: relative;
  height: 100%;
}

.row {
  // Fixes quick-box positioning (1 pixel gap)
  justify-content: space-between;
}

.row-indent {
  @include media-breakpoint-up(xs) {
    padding-left: 0;
    padding-right: 0;
  }
  @include media-breakpoint-up(lg) {
    padding-left: rem(120px);
    padding-right: rem(120px);
  }
}

.padding {

  position: absolute;
  height: rem(150px);
  bottom: 0;

  :not(.no-gutters) & {
    left: 30px;
    right: 30px;
  }

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: rem(434px);
    bottom: 0;
    background-color: $white;
    border-top-left-radius: 20px;
  }

  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: rem(434px);
    bottom: 0;
    background-image: url($page-padding);
    background-repeat: no-repeat;
    background-size: auto 100%;
  }

}