.section-management-intro {

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

  .rellax-container {
    .shape {
      right: 100%;
      margin-right: 0.08em;
      margin-top: -0.1em;
    }
  }

  .main {
  }

  .side {
    .card {
      width: auto;
      margin-top: rem(-150px);
      padding: rem(90px) rem(60px);
      @include media-breakpoint-up(lg) {
        margin-top: rem(-270px);
        margin-right: rem(-120px);
      }
    }
  }


}

.section-management-services {

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

  margin: 0 0 rem(90px) 0;

  .rellax-container {
    .shape {
      left: 100%;
      margin-left: 0.08em;
      margin-top: -0.3em;
    }
  }

}

.section-management-models {

  margin: 0 0 rem(90px) 0;

  .table {

    text-align: center;

    thead {
      th {
        //line-height: 4rem;
        white-space: nowrap;
        padding-bottom: 2.5rem;
        font-weight: 400;
        color: $black;
      }
    }

    tbody {
      th {
        padding-left: 2rem;
        font-weight: 400;
        vertical-align: middle;
        text-align: left;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        color: $black;
      }
      td {
        vertical-align: middle;
        text-align: center;
        padding: 1rem 0;
        .icon {
          //background: $white;
          border-radius: 50%;
          font-size: 200%;
          line-height: 32px;
          width: 32px;
          height: 32px;
          z-index: 100;
        }
      }
      td:last-child {
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
      }

      tr:hover {
        .icon, th {color: $white !important;}
        .icon.text-dark {opacity: 0.35;}
      }
    }

    @include media-breakpoint-down(sm) {
      .label {
        display: inline-block;
        transform: rotate(-90deg) translate(-10px, 0);
        transform-origin: center center;
        line-height: 24px;
        width: 24px;
      }

      thead th {
        height: rem(280px);
      }

      tbody th {
        padding: 0.75rem !important;
      }
    }

  }

}

.section-management-countries {

  .card {
    position: relative;
    padding: 2.5rem 1.5rem 0.5rem 1.5rem;
    border-radius: $border-radius-lg;
    background-color: #fff;
    box-shadow: $box-shadow;
    text-align: center;
    .side {color: $black;}
    .main {color: $gray-600;}
  }

  .country {

    display: inline-block;

    width: rem(2 * 64px);
    height: rem(2 * 64px);

    @include background('images/management-countries.png') {
      background-size: auto 100%;
    }


    &.country-us {background-position:  0 * 128px 0;}
    &.country-mx {background-position: -1 * 128px 0;}
    &.country-ml {background-position: -2 * 128px 0;}
    &.country-ua {background-position: -3 * 128px 0;}

  }

  @include media-breakpoint-up(xs) {
    margin-top: 5rem;
    margin-bottom: 2rem;
    .card {margin-bottom: 3rem; min-height: 9rem;}
    .card > .main {margin-top: -0.75rem; text-align: center;}
    .card > .side {margin-bottom: 2rem;}
    .card > .side > .image {margin-bottom: 0.5rem;}
  }

  @include media-breakpoint-up(sm) {
    .card > .main {padding-left: 7rem; text-align: left; margin-top: -1rem;}
    .card > .side {position: absolute; left: 2rem; top: 1.5rem;}
  }

  @include media-breakpoint-up(lg) {
    margin-bottom: 5rem;
    .card {margin-bottom: 0; height: 100%;}
    .card > .main {margin-top: 0; padding-left: 0; text-align: center;}
    .card > .side {position: static; margin: 0 0 1.5rem 0;}
  }

}

.section-management-citation {

  margin: 0 0 rem(90px) 0;

}

.section-management-words {

  margin: 0 0 rem(60px) 0;

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

}
