.btn-sm:not(.btn-link),
.btn-lg:not(.btn-link) {
  //font-weight: 300;
  border-radius: $border-radius;
}

.btn-outline {

  @include button-outline-variant($primary, $white, $primary, $primary);

  font-weight: 500;

  &:not(:hover):not(.active):not(.active):not(.disabled):not(:disabled)
  {
    background-color: $white;
  }
}

.btn-primary {
  border: none;
  background-image: linear-gradient(to top, rgba($white, 0), rgba($white, 0.5));
  background-size: 105% auto; // Fix background rendering width in Chrome
}

.btn-whitish {

  border: 1px solid #1cade4 !important;
  line-height: rem(2 * 26px);
  color: #559ced;
  text-shadow: 0 1px 0 $white;

  background-color: $border-color;
  background-image: linear-gradient(to top, rgba($white, 0), rgba($white, 1));
  background-position: 0px -1px;
  background-size: 105% 105%; // Fix background rendering width in Chrome

  &:hover, &:active, &:focus:active {
    background-color: lighten($border-color, 4.5%);
  }

  &:active {
    border: 1px solid $primary !important;
  }

}