.carousel-fade {

  //transition: height 0.6s;
  //transition-delay: 0.6s;

  .carousel-inner {
    overflow: visible;
  }

  .carousel-item {
    opacity: 0;

    // NOTE: Important is required to fix compatibility issues with Bootstrap 4.2.x bug.
    // Broken after: https://github.com/twbs/bootstrap/commit/5aed968750177ec35f99dc03e71eec3632beb844#diff-61a2c7d627355b98263c81ad79ae98f5
    transition: opacity 0.6s, transform 0.6s !important;

    // Fix slide positioning
    // Broken after: https://github.com/twbs/bootstrap/commit/96b49d7833a18028aead9a8c6caae084979865a9#diff-61a2c7d627355b98263c81ad79ae98f5
    float: none;

    // Delaying causes carousel animation to break
    // https://github.com/thednp/bootstrap.native/issues/266
    // transition-delay: 0.3s;
  }

  .carousel-item.active,
  .carousel-item-next.carousel-item-left,
  .carousel-item-prev.carousel-item-right {
    opacity: 1;
  }

  .active.carousel-item-left,
  .active.carousel-item-right {
    opacity: 0;
  }

  .carousel-item-next,
  .carousel-item-prev {
    position: absolute;
    top: 0;
  }

  .carousel-item-next.carousel-item-left,
  .carousel-item-prev.carousel-item-right {
    transform: translate(0, 0);

    @supports (transform-style: preserve-3d) {
      transform: translate3d(0, 0, 0);
    }
  }

  .carousel-item-next,
  .active.carousel-item-right {
    transform: translate(60px, 0);

    @supports (transform-style: preserve-3d) {
      transform: translate3d(60px, 0, 0);
    }
  }

  .carousel-item-prev,
  .active.carousel-item-left {
    transform: translate(-60px, 0);

    @supports (transform-style: preserve-3d) {
      transform: translate3d(-60px, 0, 0);
    }
  }

  .carousel-indicators {

    margin: 0;
    bottom: -2rem;

    li {
      width: rem(24px);
      height: rem(24px);
      margin: 0 rem(16px);
      border-radius: 50%;
      background-color: rgba($white, 0.35);
      transition: background-color 1s ease;
    }

    .active {
      background-color: $white;
    }

  }
}