.quotation {

  position: relative;
  padding: rem(90px);
  text-align: center;
  border-radius: $border-radius-lg;
  background-color: $light;
  width: 100%;

  .quote {
    text-align: inherit;
  }

  &:before {
    @extend .icon:before;
    @extend .icon-page-quotes:before;
    display: none;
    position: absolute;
    font-size: 3rem;
    font-style: italic;
    color: #cde3f6;
    width: 60px;
    text-align: left;
  }

  &:after {
    content: '';
    display: none;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: $light;
  }

  &.from-left {
    text-align: right;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;

    &:before {display: block; left: 0;}
    &:after {display: block; left: -100%;}
  }

  &.from-right {
    text-align: left;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

    &:before {display: block; right: 0;}
    &:after {display: block; right: -100%;}
  }

  &.no-quotes {
    &:before {display: none !important;}
  }

  @include media-breakpoint-up(xs) {
    //.quote {padding-left: 0;}
    &.from-left {padding-left: 0;}
    &.from-right {padding-right: 0;}
    &:before {display: none !important;}
  }

  @include media-breakpoint-up(sm) {
    &.from-left:not(.no-quotes) {padding-left: 6rem !important;}
    &.from-right:not(.no-quotes) {padding-right: 6rem !important;}
    &:before {display: inline-block !important;}
  }

}