// This removes strange height of <picture> elements
//
picture {
  font-size: 0 !important;
}

// In <noscript> mode we hide lazily-loaded images
//
html.no-js picture[data-plugin=lazy-load] {
  display: none !important;
}

// Prevent image size flickering
//
html.js picture:not(.loaded) img.img-fluid {
  width: auto !important;
  height: auto !important;
  max-width: none !important;
  max-height: none !important;
}