.icon-box {

  @extend .icon;

  position: relative;

  display: block;
  width: rem(95px);
  height: rem(100px);
  margin-bottom: rem(40px);

  color: $white;
  font-size: rem(58px);
  line-height: rem(100px);

  &:before {
    display: inline-block;
    //width: 100%;
    //height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
  }

  &:after {
    content: '';
    display: inline-block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 5;
    border-radius: $border-radius;
  }

  &.icon-box-consulting {color: $consulting; &:after {background: mix($consulting, $white, 10);}}
  &.icon-box-management {color: $management; &:after {background: mix($management, $white, 10);}}
  &.icon-box-development {color: $development; &:after {background: mix($development, $white, 10);}}
  &.icon-box-blog {color: $blog; &:after {background: mix($blog, $white, 10);}}

}

.icon-box-animated {

  @extend .icon-box;

  perspective: 100px;
  transform-style: preserve-3d;
  perspective-origin: center center;

  // Particles
  //
  > i {

    @extend .icon;

    display: inline-block;
    width: 16px;
    height: 16px;

    position: absolute;
    top: 16px;
    left: 16px;

    font-size: 8px;
    line-height: 16px;
    text-align: center;
    background: none !important;

    text-decoration: unset;

    animation-play-state: paused !important;
    animation-iteration-count: infinite !important;
    animation-timing-function: linear !important;

    &:nth-child(1) {@extend .icon-part-spot; animation: a1 2.4s; font-size: 8px * 0.45;}
    &:nth-child(2) {@extend .icon-part-square; animation: a2 3.2s; font-size: 8px * 0.60;}
    &:nth-child(3) {@extend .icon-part-donut; animation: a3 2.0s; font-size: 8px * 0.75;}
    &:nth-child(4) {@extend .icon-part-star; animation: a4 3.2s; font-size: 8px * 0.85;}
    &:nth-child(5) {@extend .icon-part-plus; animation: a5 2.4s; font-size: 8px * 0.85;}
    &:nth-child(6) {@extend .icon-part-donut; animation: a6 2.8s; font-size: 8px * 0.75;}

  }

  @keyframes a1 {
    100% {transform: rotate3d(0, 0, 0, 0deg) translate3d(28px, -26px, 0) rotate3d(0, 0, 0, 0deg);}
    50% {transform: rotate3d(0, 10, 0, 180deg) translate3d(28px, -26px, 0) rotate3d(0, 10, 0, -180deg);}
    0% {transform: rotate3d(0, 10, 0, 360deg) translate3d(28px, -26px, 0) rotate3d(0, 10, 0, -360deg);}
  }

  @keyframes a2 {
    0% {transform: rotate3d(0, 10, 0, 0deg) translate3d(40px, -20px, 0) rotate3d(0, 10, 0, 0deg);}
    50% {transform: rotate3d(0, 10, 0, 180deg) translate3d(40px, -20px, 0) rotate3d(0, 10, 0, -180deg);}
    100% {transform: rotate3d(0, 10, 0, 360deg) translate3d(40px, -20px, 0) rotate3d(0, 10, 0, -360deg);}
  }

  @keyframes a3 {
    100% {transform: rotate3d(0, 10, 0, 0deg) translate3d(36px, 19px, 0) rotate3d(0, 10, 0, 0deg);}
    50% {transform: rotate3d(0, 10, 0, 180deg) translate3d(36px, 19px, 0) rotate3d(0, 10, 0, -180deg);}
    0% {transform: rotate3d(0, 10, 0, 360deg) translate3d(36px, 19px, 0) rotate3d(0, 10, 0, -360deg);}
  }

  @keyframes a4 {
    0% {transform: rotate3d(0, 10, 0, 0deg) translate3d(23px, 34px, 0) rotate3d(0, 10, 0, 0deg);}
    50% {transform: rotate3d(0, 10, 0, 180deg) translate3d(23px, 34px, 0) rotate3d(0, 10, 0, -180deg);}
    100% {transform: rotate3d(0, 10, 0, 360deg) translate3d(23px, 34px, 0) rotate3d(0, 10, 0, -360deg);}
  }

  @keyframes a5 {
    100% {transform: rotate3d(0, 10, 0, 0deg) translate3d(-33px, 14px, 0) rotate3d(0, 10, 0, 0deg);}
    50% {transform: rotate3d(0, 10, 0, 180deg) translate3d(-33px, 14px, 0) rotate3d(0, 10, 0, -180deg);}
    0% {transform: rotate3d(0, 10, 0, 360deg) translate3d(-33px, 14px, 0) rotate3d(0, 10, 0, -360deg);}
  }

  @keyframes a6 {
    0% {transform: rotate3d(0, 10, 0, 0deg) translate3d(-30px, -19px, 0) rotate3d(0, 10, 0, 0deg);}
    50% {transform: rotate3d(0, 10, 0, 180deg) translate3d(-30px, -19px, 0) rotate3d(0, 10, 0, -180deg);}
    100% {transform: rotate3d(0, 10, 0, 360deg) translate3d(-30px, -19px, 0) rotate3d(0, 10, 0, -360deg);}
  }
}

