/***************************************
           DEFAULT TYPOGRAPHY
***************************************/

a {
  transition: color 0.15s;
}

b {
  font-weight: 500;
}

p {
  font-weight: 300;
  font-size: 1em;
  line-height: $paragraph-margin-bottom;
  margin-bottom: $paragraph-margin-bottom;
}

ul {
  @extend .list-unstyled;
}

ol {
  @extend .list-unstyled;
}


/***************************************
          CUSTOM TYPOGRAPHY
***************************************/

.text-small {
  font-size: 87.5%;
  line-height: 87.5%;
}

.text-large {
  font-size: 112.5%;
  line-height: 112.5%;
}

.text-dense {
  letter-spacing: -1px;
}

.text-broad {
  letter-spacing: 1px;
}

.text-first {
  @include media-breakpoint-up(md) {
    &:first-letter {
      display: inline-block;
      float: left;
      margin-right: 0.5rem;
      text-transform: uppercase;
      font-size: 2 * $line-height-base + rem;
      line-height: 2 * $line-height-base + rem;
      font-weight: 300;
      color: $black;
    }
  }
}

.text-more {
  &:after {
    @extend .icon:before;
    @extend .icon-chevron-right:before;
    display: inline-block;
    width: 1em;
    font-size: 0.55em;
    font-weight: 600 !important;
    margin-left: 0.35em;
  }
}

.text-dash {
  &:before {
    @extend .icon:before;
    @extend .icon-text-dash:before;
    display: inline-block;
    width: 1em;
    margin-right: 0.35em;
    vertical-align: top;
  }
}


/***************************************
                 ICONS
***************************************/

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  vertical-align: middle;
  font-size: inherit;
  line-height: 1em;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
}


/***************************************
                QUOTE
***************************************/

.quote {
  color: $black;
  font-weight: 400;
  font-style: italic;
  line-height: 1.6;
}


/***************************************
              BLOCKQUOTE
***************************************/

.blockquote {
  font-size: 1rem;

  .quote {
    margin-bottom: 1rem;
  }

  .blockquote-footer {
    @extend .text-dash;
    font-size: 100%;
    color: inherit;
  }
}


/***************************************
       RESPONSIVE TEXT WRAPPING
***************************************/

.badge > span {
  display: inline-block;
}


