.section-development-intro {

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

  .main {
  }

  .side {
    .card {
      width: auto;
      margin-top: rem(-150px);
      padding: rem(90px) rem(60px);
      @include media-breakpoint-up(lg) {
        margin-top: rem(-270px);
        margin-right: rem(-120px);
      }
    }
  }


}

.section-development-services {

  margin-bottom: rem(90px);

  .main {

    .card {

      margin: 0 0 2.5rem 0;
      padding: rem(60px);

      .title {margin-bottom: 0.75rem; font-weight: 500;}
      .words {margin-bottom: 0.75rem;}
      .tag-cloud {margin-bottom: 0;}

    }

  }

  .side {

    @include media-breakpoint-up(md) {
      padding-right: 0;
    }

    .quote {
      margin-top: 1rem;
      text-align: right;

      @include media-breakpoint-up(xs) {text-align: center;}
      @include media-breakpoint-up(md) {text-align: right; margin-right: rem(120px);}
      @include media-breakpoint-up(lg) {text-align: right; margin-right: rem(60px);}
    }

    .rellax-container {

      @include media-breakpoint-up(xs) {
        .shape {display: none;}
        .image {display: none; position: relative; top: 0; left: 0; border-radius: $border-radius-lg;}
        .other {position: relative; padding-left: 25%;}
      }

      @include media-breakpoint-up(sm) {
        .other {padding-left: 0;}
      }

      @include media-breakpoint-up(md) {
        margin-top: rem(150px);
        .image {display: block; width: rem(0.7 * 1460px); height: rem(0.7 * 900px);}
        .shape {display: block; top: rem(210px); left: rem(180px);}
        .other {top: rem(120px); left: rem(90px);}
      }

      @include media-breakpoint-up(lg) {
        .image {width: rem(1.0 * 1460px); height: rem(1.0 * 900px);}
        .shape {top: rem(270px); left: rem(180px);}
        .other {top: rem(120px); left: 0;}
      }

      @include media-breakpoint-up(xl) {
        .image {top: 0; left: rem(90px);}
        .shape {top: rem(-30px); left: 0;}
        .other {top: rem(120px); left: rem(90px);}
      }
    }

  }

}

.section-development-citation {

  margin-bottom: rem(90px);

}

.section-development-technologies {

  margin-bottom: rem(90px);

  .technologies {
    position: relative;
    margin-bottom: 0;

    > dd > .tag-cloud {margin-bottom: 0;}

    @include media-breakpoint-up(xs) {

      dt, dd {
        margin: 0;
        padding: 2rem 1rem;
        line-height: 1.25rem;
        text-align: center;
      }

      dt {
        color: $black;
        font-weight: 500;
        padding-bottom: 1rem;
      }

      dd {
        padding-top: 0;
        &:not(:last-child) {border-bottom: 1px solid $border-color;}
      }

    }

    @include media-breakpoint-up(md) {

      dt, dd {
        padding: 0.75rem 1rem;
        line-height: 1.5rem;
      }

      dt {
        clear: both;
        float: left;
        width: rem(380px);
        text-align: left;
      }

      dd {
        padding-left: rem(380px);
        text-align: right;
      }

      &:before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        border: $table-border-width solid $table-border-color;
        border-radius: $border-radius-lg;
      }
    }
  }

}

.section-development-lifecycle {

  margin-bottom: rem(90px);

  .step {
    @include media-breakpoint-up(xs) {width: rem(240px); margin: rem(40px) rem(45px);}
    @include media-breakpoint-up(md) {width: rem(240px); margin: rem(40px) rem(25px);}
    @include media-breakpoint-up(lg) {width: rem(240px); margin: rem(40px) rem(55px);}
    @include media-breakpoint-up(xl) {width: rem(240px); margin: rem(40px) rem(95px);}
    display: inline-block;
    i {width: 100%; margin: 0 0 1rem 0; font-size: 3rem; color: #62aec4;}
    b {width: 100%; margin: 0; font-weight: 500; color: $black;}
  }

}

.section-development-words {

  margin-bottom: rem(90px);

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

}