@import '../../../node_modules/pell/src/pell';

.pell {
  border: none;
}

.pell-actionbar {

  padding-bottom: 0.5rem;

  @include media-breakpoint-up(xs) {
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: center;
  }

  @include media-breakpoint-up(lg) {
    padding-left: 0;
    padding-right: 0;
    text-align: left;
  }
}

.pell-button {
  display: inline-block;
  margin: 0 0.25rem;
  font-size: 0.75rem;
  outline: none !important;
}

.pell-content {
  height: auto !important;
  min-height: 200px;
  padding: 0.5rem 0;
}

.pell-content p {
  margin-bottom: 0;
  font-family: $font-family-monospace;
  font-weight: 300;
  font-size: 1rem;
}

.pell-content ul {
  list-style: disc inside;
  padding-left: 2rem;
  li:before {content: ' ';}
}

.pell-content ol {
  list-style: decimal inside;
  padding-left: 2rem;
  li:before {content: ' ';}
}