.list {

  display: inline-block;

  margin-top: 0;
  margin-bottom: $list-margin-bottom;

  vertical-align: top;
  text-align: left;
  color: $secondary;

  > li {

    padding: 0.5rem 0;
    white-space: nowrap !important;
    color: inherit;

    > .icon {
      color: inherit;
      text-decoration: none;
      position: relative;
      &:first-child {margin-right: 0.5rem;}
      &:last-child {margin-left: 0.5rem;}
    }

    > * {
      display: inline-block;
      line-height: inherit;
      color: inherit;
      white-space: normal;
      vertical-align: top;
    }

    &:before, &:after {
      vertical-align: top;
    }
  }

  &.list-tick > li:before,
  &.list-tick-alt > li:before {
    @extend .icon:before;
    display: inline-block;
    width: 1em;
    height: 1em;
    line-height: inherit;
    margin-right: 0.5em;
  }

  &.list-tick > li:before {
    @extend .icon-list-tick:before;
  }

  &.list-tick-alt > li:before {
    @extend .icon-list-tick-alt:before;
  }


  //@include media-breakpoint-up(xs) {padding-right: rem(60px);}
  //@include media-breakpoint-up(md) {padding-right: 0;}

}

.list-stack {

  display: inline-block;

  margin: 0 rem(-60px) $list-margin-bottom rem(-60px);

  text-align: left;

  > ul {
    display: inline-block;
    margin: 0 rem(60px);
    padding-right: 0;
  }

  > ul > li {
    white-space: normal;
  }

  //@include media-breakpoint-up(xs) {}
  //@include media-breakpoint-up(md) {width: auto;}


}