.header {

  width: 100%;
  //height: rem(950px);
  height: rem(800px);

  overflow: hidden;

  background-image: url('images/header-bg.jpg');
  background-repeat: no-repeat;
  //background-size: auto 100%;
  background-size: auto rem((950 / 820) * 820px);
  background-position: center top;

  .page-title {

    color: $white;
    font-size: 2.5rem;
    font-weight: 100;
    text-align: center;
    margin-top: rem(40px);
    white-space: nowrap;

    @include media-breakpoint-up(lg) {
      text-align: left;
      margin-top: rem(180px);
    }

    @include media-breakpoint-up(sm) {
      font-size: 3.5rem;
      //font-weight: 100;
    }

  }

  .edge {
    width: auto !important;
    height: auto !important;
  }

}
