.section-contacts-main {

  .form {
    margin-top: rem(-150px);
  }

  .side {

    padding-top: 6rem;
    text-align: center;

    @include media-breakpoint-up(lg) {
      margin-left: 0.5rem;
      text-align: left;
    }

    @include media-breakpoint-up(xl) {
      margin-left: 1.5rem;
    }

    h3 {
      margin-bottom: 1.5rem;
      font-weight: 300;
      color: $primary;
    }

    p {
      display: inline-block;
      //margin-top: 0.5rem;
    }

    ul {
      display: inline-block;
      //margin-bottom: 1.5rem;
    }


    .contact-summary {

      margin: 0 0 2.5rem 0;

      .item {

        padding: 0.75rem 0;

        a {
          color: inherit;
        }

        i {
          color: $secondary;
          font-size: 1.5rem;
          margin-right: 0.5rem;
        }
      }

    }

    .resume-download {

      //margin: 0 -0.5rem;
      //padding-top: 1rem;

      //@include media-breakpoint-up(xs) {text-align: center;}
      //@include media-breakpoint-up(lg) {text-align: left;}

      display: inline-flex;

      margin: 0 -0.75rem 2.5rem -0.75rem;

      .item {

        margin: 0 0.75rem;

        text-align: center;

        //&.pdf {color: #d25745;}
        //&.word {color: #3c64bb;}
        //&.pages {color: #e79222;}

        &:nth-child(1) {@include link-color($red);}
        &:nth-child(2) {@include link-color($purple);}
        &:nth-child(3) {@include link-color($orange);}

        a {
          display: inline-block;
          color: inherit;
          text-decoration: none;

          i {
            display: block;
            width: auto;
            font-size: 3.5rem;
            line-height: 3.5rem;
          }

          b {
            font-size: 75%;
            font-weight: 600;
            color: $gray-600;
          }

          &:hover {

          }
        }
      }


    }
  }

}

.section-contacts-privacy {

  .container {
    margin-top: rem(150px);
    margin-bottom: rem(150px);
    padding: 0 1.5rem;
  }

  .rellax-container {
    .shape {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: -1;
      margin-right: -40px;
    }
  }

  @include media-breakpoint-up(xs) {
    .main {text-align: center;}
    .side {text-align: center;}
    .image {margin: 1rem 0 1.5rem 0;}
    .title {margin: 0 0 0.5rem 0;}
    .content {}
  }

  @include media-breakpoint-up(lg) {
    .main {text-align: left;}
    .side {text-align: right;}
    .image {float: left; margin: 1rem 3rem 0 0;}
    .title {display: block; margin-top: 0.5rem;}
    .content {display: block;}
    .link {margin-top: 0.5rem;}
  }

