.section-blog-list-intro {

  margin-bottom: rem(60px);

  .side {

    .card {

      // This is required to stretch box width in both sides
      // with negative left/right margins.
      width: auto;

      padding: rem(60px);

      .photo {
        width: rem(300px);
        height: rem(300px);
        border-radius: 50%;
        box-shadow: 0 0 0 rem(4px) rgba($white, 0.85);
      }

      .quote {
        color: $white;
        font-weight: 300;
      }

      @include media-breakpoint-up(xs) {
        margin-top: rem(-150px);
        padding-left: rem(60px);
        padding-right: rem(60px);
        .photo {display: block; margin: 0 auto rem(60px);}
        .quote {margin-bottom: 1.5rem;}
        .name {padding-bottom: 1rem; font-weight: 400;}
      }

      @include media-breakpoint-up(sm) {
        padding-left: rem(120px);
        padding-right: rem(120px);
      }

      @include media-breakpoint-up(md) {
        .carousel-inner {width: auto; margin-left: 10rem;} // Compensate space for the photo
        .carousel-item {text-align: right;}
        .photo {float: left; margin: 0;}
        .quote {}
        .name {padding-bottom: 0;}
      }

      @include media-breakpoint-up(lg) {
        margin-top: rem(-210px);
        margin-left: rem(-60px);
        margin-right: rem(-60px);
        padding-left: rem(120px);
        padding-right: rem(120px);
      }

    }

    @include media-breakpoint-up(lg) {
      padding-left: 0;
      padding-right: 0;
    }

  }
}

.section-blog-list-filter {

  margin-bottom: rem(120px);
  //padding: 0 rem(180px);

  .main {

    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .btn {

      @include media-breakpoint-up(xs) {
        width: 100%;
        margin: 0 0 rem(48px) 0;
      }

      @include media-breakpoint-up(md) {
        width: auto;
        margin: 0 rem(24px) rem(48px) rem(24px);
      }

      &.active {
        cursor: default;
        pointer-events: none;
        border-width: 2px;
        background-color: $white;
        color: $primary;
        font-weight: 500;
      }
    }
  }

}

.section-blog-list-posts {

  margin-bottom: rem(120px);

  .rellax-container:first-child {
    .shape-2 {top: 0; right: 0; margin-top: 0.55em; margin-right: -0.5em;}
    .shape-1 {top: 0; left: 0; margin-left: -0.75em; margin-top: -0.75em;}
  }

  .rellax-container:last-child {
    .shape-1 {top: 0; left: 0; margin-left: -0.125em; margin-top: -0.20em;}
  }

  .search {

    padding: rem(90px);
    border: 3px solid $white;
    border-radius: $border-radius-lg;
    position: relative;

    background-image: $gradient-green;
    box-shadow: $box-shadow;

    .form-control {
      padding-right: rem(2 * 50px);
      border: 1px solid transparent;
      &:focus {
        border-color: #38bbb1;
        box-shadow: $outline-white;
      }
    }

    .btn {
      position: absolute;
      right: rem(90px);
      .icon {
        font-weight: 500;
      }
    }

    @include media-breakpoint-up(lg) {
      float: right;
    }

  }

  .post-list > .search,
  .post-list > .post {

    @include media-breakpoint-up(xs) {
      margin: 0 0 rem(120px) 0;
      //padding: rem(90px);
    }

    @include media-breakpoint-up(md) {
      width: 100%;
    }

    @include media-breakpoint-up(lg) {
      width: calc(50% - 150px / 2);
      margin: 0 0 rem(150px) 0;
    }

  }

  .post-list > .post {

    position: relative;

    @include media-breakpoint-up(xs) {
      > .card-body {text-align: left; padding: rem(60px);}
      > .card-body > .card-title {margin-bottom: 1rem;}
      > .card-body > .card-title:after {display: none;}
      > .card-body > .tag-cloud {margin-bottom: 1.25rem;}
      > .card-body > .words {margin-bottom: 1rem;}
      > .card-body > .card-title > .badge {font-size: 0.875rem; line-height: 1rem; white-space: pre-wrap;}
      > .card-footer {display: flex; justify-content: space-between;}
    }

    @include media-breakpoint-up(md) {
      > .card-body > .card-title {padding-right: rem(150px);}
      > .card-body > .card-title > .badge {position: absolute; top: 50%; right: 0; transform: translate(0, -50%);}
    }

    @include media-breakpoint-up(lg) {
      > .card-body > .card-title {padding-right: 0;}
      > .card-body > .card-title:after {display: block;}
      > .card-body > .card-title > .badge {width: rem(150px);box-shadow: 0 0 0 2px $white;}

      &.card-title-left {
        float: right;
        > .card-body {text-align: left;}
        > .card-body > .card-title > .badge {left: -$card-spacer-x - rem(150px); transform: translate(-50%, -50%);}
      }

      &.card-title-right {
        float: left;
        > .card-body {text-align: right;}
        > .card-body > .card-title > .badge {right: -$card-spacer-x - rem(150px); transform: translate(50%, -50%);}
      }
    }

  }

}