.modal-body-reviews {

  p {
    line-height: rem(72px);
  }

  .info {

    text-align: center;

    .face {
      display: block;
      margin: 0 auto;
      font-size: rem(200px);
      box-sizing: padding-box;
      box-shadow: inset 0 0 0 1px $gray-400;
    }

    .name {
      display: block;
      margin: 0 auto;
      height: rem(200px);
      padding: 1.5rem 0;
      color: $black;
      line-height: 2rem;
    }

    .btn {

      margin: 1rem 0 0.5rem 0;

      > .icon {
        margin-right: 0.5rem;
        font-size: $h5-font-size;
        line-height: 1.125rem;
      }
    }

    @include media-breakpoint-up(md) {
      text-align: left;
      .face {float: left;}
      .name {float: left; margin-left: 2rem; margin-right: -2.5rem;}
      .btn {float: right; margin: 2rem 0;}
    }


  }

  .main {
    margin-top: 2rem;

    @include media-breakpoint-up(xs) {
      text-align: center;
    }

    @include media-breakpoint-up(md) {
      text-align: left;
    }

  }
}