.navbar {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  padding: 3rem 0;
  border-bottom: 1px solid $gray-400;

  @include media-breakpoint-up(lg) {
    flex-direction: row;
    justify-content: space-between;
    padding: 1.5rem 0;
  }

  .edge {
    @extend .icon;
    @extend .icon-page-logo;
    display: block;
    width: auto !important;
    height: 42px !important;
    margin: 0 0 1.5rem 0;
    font-size: 2.6rem;
    line-height: 42px;
    color: #fff;
    text-decoration: none;
    vertical-align: top;

    // Fix logo disappearing in MD
    z-index: 1;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }
  }

  .menu {

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include media-breakpoint-only(xs) {
      margin: 0 rem(-60px);
    }

    // Fix hover flickering on index.html
    //
    z-index: 1;

    .item {

      margin: 0 1rem;

      color: $white;
      text-decoration: none;
      line-height: 2.25rem;

      transition: opacity 0.15s ease-in-out;
      opacity: 0.75;

      &:hover {
        opacity: 1;
      }

      .btn {
        font-size: 93%;
        font-weight: 500;
        line-height: 1.8;
      }

      .btn-contact {
        @include button-outline-variant($white, $black, $white, $white);
      }

      &:first-child {
        margin-right: 1rem;
        margin-left: 1rem;

        @include media-breakpoint-up(sm) {
          margin-left: 0;
        }

        @include media-breakpoint-up(xl) {
          margin-left: 1rem;
        }
      }

      &:nth-last-child(2) {
        margin-right: 1rem;
        margin-left: 1rem;

        @include media-breakpoint-up(sm) {
          margin-right: 0;
        }

        @include media-breakpoint-up(xl) {
          margin-right: 1rem;
        }
      }

      &:last-child {
        display: none;
        margin-right: 0;
        margin-left: 1rem;

        @include media-breakpoint-up(xl) {
          display: block;
        }
      }

    }
  }

  &.navbar-home {

    padding-top: 3rem;
    padding-bottom: 3rem;
    border: none;

    @include media-breakpoint-up(md) {
      align-items: flex-end;
      flex-direction: column;
    }

    @include media-breakpoint-up(xl) {
      flex-direction: row;
    }

    .edge {
      margin-bottom: 1.5rem;
      color: $white;

      @include media-breakpoint-up(xl) {
        margin-bottom: 0;
        color: $primary;
      }
    }

    .menu {
      margin-top: 0;

      @include media-breakpoint-up(xl) {
        margin-top: 0.5rem;
      }

      .item {
        font-weight: 400;
      }
    }
  }
}