.section-base-consultation {

  margin-bottom: 0 !important;

  .wrap {
    padding: 3rem 1.5rem 3.5rem 1.5rem;
    border-top: 1px solid $border-color;
  }

  .main {
    padding-bottom: 1.5rem;
    text-align: center;

    @include media-breakpoint-up(lg) {
      padding-bottom: 0;
      text-align: left;
    }

    .title {
      color: $primary;
    }

    .words {
      margin: 0;
    }

  }

  .side {
    text-align: center;
    @include media-breakpoint-up(lg) {
      text-align: right;
    }
  }
}