.section-consulting-intro {

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

  .main {
  }

  .side {
    .card {
      width: auto;
      margin-top: rem(-150px);
      padding: rem(90px) rem(60px);
      @include media-breakpoint-up(lg) {
        margin-top: rem(-270px);
        margin-right: rem(-120px);
      }
    }
  }

}

.section-consulting-tasks {

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

  .side {
    .rellax-container {
      .shape {
        top: 0;
        left: 100%;
        margin-left: -0.5em;
        margin-top: -0.1em;
      }
    }
  }

  .main {
  }

}

.section-consulting-diagram {

  margin: 0 0 rem(150px) 0;
  padding: rem(180px) 0;
  position: relative;
  text-align: center;

  .title {
    line-height: 2.5rem;
    margin-bottom: rem(120px);

    &.title-left {float: left; width: calc(50% - 120px / 2);}
    &.title-right {float: right; width: calc(50% - 120px / 2);}
  }

  @include media-breakpoint-only(sm) {

    //margin: 0 0 rem(120px) 0 !important;

    padding: rem(120px) 0;

    //&:after, &:before {display: none;}

    .title {
      margin-bottom: rem(60px);
    }

  }

}

.section-consulting-cicd {

  @include media-breakpoint-up(xs) {margin-bottom: rem(90px); text-align: center;}
  @include media-breakpoint-up(md) {margin-bottom: rem(180px); text-align: left;}

  .main {
    .image {
      max-width: 100%;
      height: auto;
    }
  }
  .side {
    .image {
      margin-top: 1rem;
      margin-bottom: 3rem;
      width: 80%;
      @include media-breakpoint-up(md) {width: 100%; float: right}
    }
  }

}

.section-consulting-sdlc {

  @include media-breakpoint-up(xs) {margin-bottom: rem(90px); text-align: center;}
  @include media-breakpoint-up(md) {margin-bottom: rem(180px); text-align: right;}

  .side {
    .image {
      margin-top: 1rem;
      margin-bottom: 3rem;
      width: 60%;
      @include media-breakpoint-up(md) {width: 90%; float: left;}
    }
  }

  .main {
  }

}

.section-consulting-citation {

  margin-bottom: rem(90px);

}

.section-consulting-words {

  margin-bottom: rem(90px);

  @include media-breakpoint-up(xs) {text-align: center;}
  @include media-breakpoint-up(md) {text-align: left;}

}