.card {

  // Remove borders from all cards
  border: none;

  // This is required to stretch box width in both sides
  // with negative left/right margins.
  width: auto;

  // Default card display
  display: block;

  // Default card margin
  margin: 0 0 2.5rem 0;

  // Default card padding
  padding: rem(90px) rem(60px);

  // Border radius
  border-radius: $card-border-radius;

  // Center text
  text-align: center;

  // Card shadow
  box-shadow: $box-shadow;

  > .card-body > .card-title {
    position: relative;
    margin-bottom: 0;
  }

  // Paddings
  > .card-body {
    padding: rem(90px) rem(60px);

    &:not(:last-child) {padding-bottom: 1.5rem;}
    &:not(:first-child) {padding-top: 1.5rem;}
  }

  // Colors
  > .card-header,
  > .card-footer {
    //display: flex;
    //justify-content: space-between;
    padding: 1rem rem(60px);
    background-color: transparent;
  }

  // Card intro
  &.card-intro {
    position: relative;
    color: $white;
    background-image: $gradient-blue;
    background-size: 105% auto;
    background-color: #fff;
    box-shadow: $box-shadow;
    &.card-intro-shape {
      overflow: hidden;
      &:before {
        content: '';
        font-size: rem(400px);
        position: absolute;
        right: -0.35em;
        top: -0.35em;
        display: block;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        background: rgba($white, 0.15);
      }
    }
  }

  // Card green
  &.card-green {

    display: flex;
    padding: 0;
    box-shadow: none;

    @include media-breakpoint-up(xs) {
      border: 1px solid $border-color;
      border-radius: $border-radius-lg;
      overflow: hidden;
    }

    @include media-breakpoint-up(md) {
      background-image: $gradient-green;
      > .card-body {
        margin: rem(80px);
        background: $white;
        box-shadow: $box-shadow-sm;
      }
    }

  }

  // Card panel
  &.card-panel {

    display: flex;
    padding: 0;
    box-shadow: none;

    > .card-body {
      border: 1px solid $card-border-color;
      background: $light;
    }

    > .card-body:first-child,
    > .card-header {
      border-top-left-radius: $card-border-radius;
      border-top-right-radius: $card-border-radius;
    }

    > .card-body:last-child,
    > .card-footer {
      border-bottom-left-radius: $card-border-radius;
      border-bottom-right-radius: $card-border-radius;
    }

    > .card-header,
    > .card-footer {
      color: $white;
      background-image: $gradient-mixed;
      border: none;
      margin: -1px 0;
    }

  }

  &.card-arrow {

    display: flex;
    padding: 0;

    &.card-arrow-left:before,
    &.card-arrow-right:before,
    &.card-arrow-top:after,
    &.card-arrow-bottom:after,
    &.card-title-right > .card-body > .card-title:after,
    &.card-title-left > .card-body > .card-title:after {
      cursor: default;
      display: block;
      position: absolute;

      @include background('images/card-arrows.png') {
        background-repeat: no-repeat;
      }
    }

    &.card-arrow-left:before,
    &.card-arrow-right:before,
    &.card-title-right > .card-body > .card-title:after,
    &.card-title-left > .card-body > .card-title:after {
      width: rem(80px);
      height: rem(140px);
      background-size: percentage(300 / 80) percentage(300 / 140);
    }

    &.card-arrow-top:after,
    &.card-arrow-bottom:after {
      width: rem(140px);
      height: rem(80px);
      background-size: percentage(300 / 140) percentage(300 / 80);
    }

    // Arrow left
    &.card-arrow-left:before,
    &.card-title-left > .card-body > .card-title:after {
      content: '';
      top: 50%;
      left: rem(-80px);
      margin-top: rem(-140px / 2);
      background-position: rem(-300px + 80px) rem(-80px);
    }

    // Arrow right
    &.card-arrow-right:before,
    &.card-title-right > .card-body > .card-title:after {
      content: '';
      top: 50%;
      right: rem(-80px);
      margin-top: rem(-140px / 2);
      background-position: 0 rem(-80px);
    }

    // Arrow top
    &.card-arrow-top:after {
      content: '';
      top: rem(-80px);
      left: 50%;
      margin-left: rem(-140px / 2);
      background-position: rem(-80px) rem(-300px + 80px);
    }

    // Arrow bottom
    &.card-arrow-bottom:after {
      content: '';
      bottom: rem(-80px);
      left: 50%;
      margin-left: rem(-140px / 2);
      background-position: rem(-80px) 0;
    }

    &.card-title-right,
    &.card-title-left {
      > .card-body > .card-title {
        position: relative;
      }
      > .card-body > .card-title:after {
        margin-top: 0;
      }
    }

    &.card-title-right {
      > .card-body > .card-title:after {
        transform: translate($card-spacer-x, -50%);
      }
    }

    &.card-title-left {
      > .card-body > .card-title:after {
        transform: translate(-$card-spacer-x, -50%);
      }
    }

  }

  &.card-responsive {
    @include media-breakpoint-down(sm) {
      margin: 0 0 1.75rem 0 !important;
      padding: rem(45px) 0 0 0 !important;

      box-shadow: none;
      border-radius: unset;
      border-top: 1px solid $border-color;

      &:last-child {
        padding-bottom: rem(45px) !important;
        border-bottom: 1px solid $border-color;
      }
    }
  }


  > .link-zoom {

    display: block;
    position: relative;
    overflow: hidden;

    &.card-img-top > picture > img {
      transition: transform 0.15s;
      transform-origin: center bottom;
    }

    &.card-img-bottom > picture > img {
      transition: transform 0.15s;
      transform-origin: center top;
    }

    &:hover {
      > picture > img {
        transform: scale3d(1.05, 1.05, 1.05);
      }
    }

  }

}