.carousel-zoom {

  .carousel-inner {
    overflow: visible;
  }

  .carousel-item {
    transition: transform 1s, opacity 1s;
    transform-origin: center bottom;
    display: none;
  }

  .carousel-item.active,
  .carousel-item-next,
  .carousel-item-prev {
    display: block;
    opacity: 1;
  }

  .carousel-item-next,
  .carousel-item-prev {
    position: absolute;
    bottom: 0;
    opacity: 0.75;
  }

  .carousel-item-next.carousel-item-left,
  .carousel-item-prev.carousel-item-right {
    // opacity: 1;

    transform: scale(1) translate(0, 0);

    @supports (transform-style: preserve-3d) {
      transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
    }
  }

  .carousel-item-next.carousel-item-left {
    opacity: 1;
  }

  .carousel-item-prev.carousel-item-right {
    opacity: 1;
  }

  .carousel-item-next,
  .active.carousel-item-right {

    opacity: 0.75;
    transform: scale(percentage(1279 / 1609) / 100%) translate(0, 35px * (1609 / 1279));

    @supports (transform-style: preserve-3d) {
      transform: scale3d(percentage(1279 / 1609) / 100%, percentage(1279 / 1609) / 100%, percentage(1279 / 1609) / 100%) translate3d(0, 35px * (1609 / 1279), 0);
    }
  }

  .carousel-item-prev,
  .active.carousel-item-left {

    opacity: 0;
    transform: scale(percentage(1609 / 1279) / 100%) translate(0, -35px * (1279 / 1609));

    @supports (transform-style: preserve-3d) {
      transform: scale3d(percentage(1609 / 1279) / 100%, percentage(1609 / 1279) / 100%, percentage(1609 / 1279) / 100%) translate3d(0, -35px * (1279 / 1609), 0);
    }
  }

  .carousel-item-next {
    opacity: 0.75;
    z-index: -1;
  }

  .carousel-item-prev {
    opacity: 0;
    z-index: 100;
  }

  // Fix jumping carousel.
  // This code requires to set height of .carousel-inner
  // to maximum slide height.
  .carousel-inner {
    position: relative;
    .carousel-item {
      position: absolute;
      bottom: 0;
    }
  }

  .carousel-indicators {

    margin: 0;
    bottom: rem(-24px - 160px);

    li {
      width: rem(24px);
      height: rem(24px);
      margin: 0 rem(16px);
      border-radius: 50%;
      background-color: #dedede;
      transition: background-color 1s ease;
    }

    .active {
      background-color: $primary;
    }

  }

  // Animation customizations
  //

  .carousel-item {

    .review .next {
      @include transition(opacity 1s ease);
      opacity: 0.75;
    }

  }

  .active.carousel-item-left,
  .carousel-item-prev.carousel-item-right
  {
    .review .next {
      visibility: hidden;
    }
  }

  .carousel-item-next.carousel-item-left,
  .active {
    .review .next {
      opacity: 0.75;
    }
  }

  .carousel-item-next,
  .active.carousel-item-right {
    .review .next {
      opacity: 0;
    }
  }

}