.modal {

  width: 100%;
  height: 100%;

  // Enforce over script's value
  padding-right: 0 !important;

  // Prevent width flickering
  .modal-open & {
    overflow-y: scroll;
  }

  &.modal-fast-slides {
    .carousel-item {
      transition: none !important;
    }
  }

  &.modal-fast-height {
    .modal-dialog {
      .modal-content {
        transition: none !important;
      }
    }
  }

  // We cannot set transition to none, since
  // it will break fade out modal animation.
  // Instead, we make transition invisible.
  // Hopefully, browsers will understand this.
  &.fade {
    transition-property: opacity !important;
    transition-duration: 0.5s !important;
    opacity: 1 !important;
  }

}

.modal-dialog {

  //will-change: opacity !important;

  transition: opacity 0.35s ease-out !important;
  transform: translate3d(0, 0, 0) !important;

  .modal.fade & {
    opacity: 0;
  }

  .modal.show & {
    opacity: 1;
  }

  &.loader {

    height: 100%;
    text-align: center;

    .modal-content {

      border: none !important;
      background: none !important;

      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 100;

      width: rem(800px);
      height: rem(600px);
      margin: rem(-300px) 0 0 rem(-400px);
    }

    .modal.fade & {
      transition-delay: 0s !important; // Hide delay
      opacity: 0;
    }

    .modal.show & {
      transition-delay: 0s !important; // Show delay
      opacity: 1;
    }

  }

}

.modal-dialog {

  width: 100%;
  height: auto;

  // Remove top/bottom margins, but keep it centered position
  margin: 0 auto;

  @include media-breakpoint-up(xs) {
    max-width: 100%;

    .modal-content {border-radius: unset;}
  }

  @include media-breakpoint-up(sm) {

    max-width: rem(1080px + 80px);

    // Use transparent borders for top/bottom margins
    border-top: 50px solid transparent;
    border-bottom: 50px solid transparent;

    .modal-content {border-radius: $border-radius-xl;}
  }

  @include media-breakpoint-up(md) {
    max-width: rem(1440px + 80px);
  }

  @include media-breakpoint-up(lg) {
    max-width: rem(1920px + 80px);
  }

  @include media-breakpoint-up(xl) {
    max-width: rem(2280px + 80px);
  }

}

.modal-content {

  width: 100%;
  height: 100%;

  // Hide overflowing content before animation
  overflow-y: hidden;

  // Hide carousel horizontal overflows
  overflow-x: hidden;

  border: rem(40px) solid rgba($black, 0.15);

}

.modal-header,
.modal-footer {

  //border-left: rem(90px) solid $white;
  //border-right: rem(90px) solid $white;
  padding: rem(70px) rem(90px);
  background-color: $white;

  position: relative;

  //&:before {
  //  content: '';
  //  position: absolute;
  //  left: 0;
  //  right: 0;
  //  display: block;
  //  width: 100%;
  //  height: 1px;
  //  background-color: $border-color;
  //}

}

.modal-header {

  //border-bottom: none;
  //&:before {bottom: 0;}

  border-bottom: 1px solid $border-color;

  height: rem(100px);

  padding-bottom: 4rem;

  .modal-title {
    color: $black;
  }

  .close {

    width: rem(2 * 60px);
    height: rem(2 * 60px);

    position: relative;
    top: 0.4rem;
    color: $primary;
    margin: -1rem;
    // Fix missing pixel in Chrome
    border-left: 1px solid transparent;

    // Remove active outline
    outline: none !important;

    opacity: 0.5;
    transition: opacity 0.15s;

    &:hover {
      color: $primary !important;
      opacity: 1 !important;
    }
  }

}

.modal-body {

  margin: 0 rem(90px);
  //margin: 0;
  padding: 0;

  .carousel {
    margin: rem(60px) 0;
  }

  .carousel-item {
    text-align: left;
    padding: 0;
  }

}

.modal-footer {

  // By default it's display: flex,
  // but let's fix it for IE 10.
  //
  display: block;

  // Should cover slide being slid
  //
  z-index: 1000;

  // Top border
  //
  border-top: 1px solid $border-color;

  // Prevent corner flickering
  border-bottom-right-radius: rem(20px);
  border-bottom-left-radius: rem(20px);

  //border-top: none;
  //&:before {top: 0;}

}
