.footer {

  background-image: $gradient-footer;
  background-repeat: repeat-x;

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .container-fluid {
    padding: 0;
  }

  .edge {
    @extend .icon;
    @extend .icon-page-logo;
    z-index: 1;
    display: block;
    width: auto !important;
    height: 42px !important;
    margin: 3rem 0;
    font-size: 2.6rem;
    line-height: 42px;
    color: #fff;
    text-decoration: none;
    vertical-align: top;
  }

  .menu {

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    @include media-breakpoint-only(xs) {
      margin: 0 rem(-60px);
    }

    .item {
      margin: 0 1.5rem 0;
      padding: 0;

      color: $white;
      line-height: 2.25rem;
      text-decoration: none;

      transition: opacity 0.15s ease-in-out;
      opacity: 0.75; &:hover {opacity: 1;}

    }
  }

  .copy {

    display: flex;
    align-items: center;

    margin: 1.5rem 0;
    padding: 1.5rem 0 0.5rem;

    color: $white;

    @include media-breakpoint-up(lg) {
      margin: 3rem 0;
    }

    span {
      white-space: nowrap;
      font-size: 0.875rem;
      font-weight: 300;
      color: $white;
      opacity: 0.4;
    }

    &:before,
    &:after {
      display: block;
      content: '';
      width: 100% !important;
      height: 1px;
      background-color: $white;
      opacity: 0.2;
      flex: 0 1 100%;
    }

    &:before {margin-right: 2rem;}
    &:after {margin-left: 2rem;}

  }

  .back-to-top {

    width: rem(115px);
    height: rem(115px);
    border-radius: 50%;

    position: absolute;
    top: -105%;
    right: 0;
    z-index: 100;

    background-image: $gradient-blue;

    color: $white;
    text-align: center;
    font-size: rem(115px * 0.3);
    line-height: rem(120px);

    text-decoration: none;

    transition: transform 0.15s;
    transform: scale3d(1, 1, 1);

    .icon {
      display: inline;
    }

    &:hover {
      transform: scale3d(1.1, 1.1, 1.1);
    }

  }

  .social-icons {

    display: flex;
    justify-content: center;

    margin: 0 0 1.5rem 0;
    padding: 0 0 1rem 0;

    font-size: 1.75rem;

    @include media-breakpoint-up(lg) {
      margin-bottom: 3rem;
    }

    .icon {
      color: $white;
      text-decoration: none;

      @include media-breakpoint-up(sm) {
        margin: 0 1.5rem;
      }

      &.icon-social-medium:before {font-size: 100%;}
      &.icon-social-facebook:before {font-size: 80%;}
      &.icon-social-twitter:before {font-size: 92%;}
      &.icon-social-rss:before {font-size: 75%;}

      transition: opacity 0.15s ease-in-out;
      opacity: 0.75; &:hover {opacity: 1;}

    }
  }
}