.title {

  position: relative;

  color: $black;
  font-weight: 400;
  //margin-bottom: 1rem;
  //line-height: 1.5;
  //white-space: nowrap;

  // Fixes Chrome bug of disappearing title
  //
  z-index: 1000;

  &:before,
  &:after {
    display: inline-block;
    width: 240px;
    height: 1px;
  }

  &.line-both,
  &.line-left {
    display: inline-flex;
    flex-direction: column;
    &:after {
      content: '';
      margin: 1rem 0 0 1px;
      //background-color: $border-color;
      background-image: linear-gradient(to right, $border-color, rgba($border-color, 0));
    }
  }

  &.line-both,
  &.line-right {
    display: inline-flex;
    flex-direction: column;
    &:before {
      content: '';
      margin: 1rem 1px 0 0;
      //background-color: $border-color;
      background-image: linear-gradient(to left, $border-color, rgba($border-color, 0));
    }
  }

  &.line-both {
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    &:after {margin: 0 0 0 2rem;}
    &:before {margin: 0 2rem 0 0;}
  }

}