@mixin scale($width, $height, $scale: 0.85) {
  $width: $width * $scale;
  $height: $height * $scale;
  @include media-breakpoint-up(xs) {font-size: rem(720 * $width / 1140);}
  @include media-breakpoint-up(sm) {font-size: rem(720 * $width / 1140);}
  @include media-breakpoint-up(md) {font-size: rem(960 * $width / 1140);}
  @include media-breakpoint-up(lg) {font-size: rem(960 * $width / 1140);}
  @include media-breakpoint-up(xl) {font-size: rem(1140 * $width / 1140);}
  width: ($width / $width + em);
  height: ($height / $width + em);
}
.shape {

  position: absolute;
  z-index: -1;

  &-1 {@include scale(1658px, 1658px, 0.85);}
  &-2 {@include scale(1658px, 1658px, 0.85);}
  &-3 {@include scale(1200px, 580px, 1);}
  &-4 {@include scale(1601px, 801px, 1);}
  &-5 {@include scale(1024px, 896px, 1);}
  &-6 {@include scale(1046px, 470px, 1);}

  img {
    vertical-align: top;
  }

}